.hamburger{
  display: block;
  width: 30px;
  height: 8px;
  position: relative;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
  z-index:5;

}
.hamburger span{
  display: block;
  position: absolute;
  height: 1px;
  width: 100%;
  background: #ffffff;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: .25s ease-in-out;
}
.hamburger span:nth-child(1) {
  width: 30px;
  top: 0;
}
.hamburger span:nth-child(2) {
  width: 20px;
  top: 7px;
}
