.btn{
  font-size: 1rem;
  line-height: normal;
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  cursor: pointer;
  border: none;
  text-align: center;
  text-decoration: none;
  box-shadow: none;
  outline: none;
  padding: 0;
  background-color: transparent;

}

.primary-btn{
  @extend .btn;
  height: 50px;
  min-width: 190px;
  padding: 0 40px;
  border-radius: 25px;
  border: 0;
  color: #ffffff;
  background-color:$primary-btn-color;
  font-size: 18px;
  transition: all .25s;
  @media (max-width: 480px) {
    height: 44px;
    font-size: 16px;
    padding: 0 20px;
  }
  @media (max-width: 400px) {
    font-size: 14px;
    height: 36px;
  }
  &:hover{
    @media (min-width: 1201px) {
      color: #ffffff;
      background-color: $primary-btn-hover-color;
    }
  }
  &:active{
    @media (min-width: 1201px) {
      color: #ffffff;
      background-color: $primary-btn-active-color;
    }
  }
  &[disabled]{
    background-color: $disabled-color;
    color: #ffffff;
    cursor: default;
    opacity: .5;
    &:hover{
      background-color: $disabled-color;
      color: #ffffff;
    }
  }
  .svg-icon{
    fill: #ffffff;
    margin-right: 6px;
  }
  &--accent {
    background-color: $theme-color-4-light;
    &:hover {
      background-color: $theme-color-4;
    }
    &:active {
      background-color: $theme-color-4-dark;
    }
  }
  &--neutral {
    color: $text-color-base;
    background-color: #E7E7E7;
  }
}

.secondary-btn {
  @extend .primary-btn;
  background-color: transparent;
  border: 1px solid $theme-color;
  color: $text-color-base;
  transition: all .25s;
  &[disabled]{
    border-color: $text-color-light;
    color: $text-color-light;
    cursor: default;
    opacity: .5;
    &:hover{
      border-color: $text-color-light;
      color: $text-color-light;
    }
  }
  &--light {
    border-color: #ffffff;
    color: #ffffff;
    &:hover{
      @media (min-width: 1201px) {
        border-color: transparent;
        color: #ffffff;
        background-color: $primary-btn-hover-color;
      }
    }
    &:active{
      @media (min-width: 1201px) {
        border-color: transparent;
        color: #ffffff;
        background-color: $primary-btn-hover-color;
      }
    }
    &[disabled]{
      cursor: default;
      opacity: .5;
      &:hover{
        border-color: #ffffff;
        color: #ffffff;
      }
    }
  }
  &--neutral {
    border-color: $text-color-light;
    color: $text-color-light;
    &:hover{
      @media (min-width: 1201px) {
        border-color: transparent;
        color: #ffffff;
        background-color: $primary-btn-hover-color;
      }
    }
    &:active{
      @media (min-width: 1201px) {
        border-color: transparent;
        color: #ffffff;
        background-color: $primary-btn-hover-color;
      }
    }
    &[disabled]{
      cursor: default;
      opacity: .5;
      &:hover{
        border-color: #ffffff;
        color: #ffffff;
      }
    }
  }
  .svg-icon {
    fill: $theme-color;
  }
  &:hover {
    .svg-icon {
      fill: #ffffff;
    }
  }
}

.light-btn {
  @extend .primary-btn;
  background-color: #ffffff;
  color: $text-color-base;
  &:hover{
    @media (min-width: 1201px) {
      color: #ffffff;
      background-color: $primary-btn-hover-color;
    }
  }
  &:active{
    @media (min-width: 1201px) {
      color: #ffffff;
      background-color: $primary-btn-active-color;
    }
  }
  &[disabled]{
    cursor: default;
    opacity: .5;
    &:hover{
      background-color: #ffffff;
      color: $text-color-base;
    }
  }
  .svg-icon{
    fill: $text-color-base;
  }
}

.accent-btn {
  @extend .primary-btn;
  background-color: $theme-color-2;
  color: #ffffff;
}

.sm-btn {
  min-width: unset;
  height: 36px;
  border-radius: 18px;
  padding: 0 20px;
  font-size: 14px;
}

.inline-btn {
  min-width: auto;
  height: 34px;
  border-radius: 18px;
  padding: 0 20px;
  margin: 0 .25em;
  font-size: inherit;
  color: $theme-color;
}

.wide-btn{
  width: 100%;
}

.wide-mob-btn {
  @media (max-width: 540px) {
    width: 100%;
  }
}

.text-btn {
  @extend .btn;
  color: $text-color-light;
  font-size: 14px;
  line-height: 1;
  &:hover {
    color: $theme-color;
  }
}

.video-btn {
  @extend .btn;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border: 1px solid $border-color;
  transition: all .25s;
  .svg-icon {
    fill: $theme-color;
  }
  &:hover {
    background-color: $theme-color;
    border-color: transparent;
    .svg-icon {
      fill: #ffffff;
    }
  }
}

.btns {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin: 20px -10px 0;
  @media (max-width: 480px) {
    margin: 10px -10px 0;
  }
  & > * {
    margin: 20px 10px 0;
    @media (max-width: 480px) {
      margin-top: 10px;
    }
  }
  &--left {
    justify-content: flex-start;
  }
  &--grid {
    justify-content: flex-start;
    & > * {
      width: calc(20% - 20px);
      @media (max-width: 1200px) {
        width: calc(25% - 20px);
      }
      @media (max-width: 1000px) {
        width: calc(33.3333% - 20px);
      }
      @media (max-width: 768px) {
        width: calc(50% - 20px);
      }
      @media (max-width: 480px) {
        width: calc(100% - 20px);

      }
    }
  }
}

