ul, ol {
  list-style: none;
  padding: 0;
  margin: 0;
  li{
    padding: 0;
    margin: 0;
    display: flex;
    .icon-wrap,
    .svg-icon {
      flex-shrink: 0;
    }
  }
}

ul.titled-list {
  li{
    display: block;
    margin-bottom: 30px;
    padding-left: 20px;
    position: relative;
    @media (max-width: 480px) {
      margin-bottom: 16px;
    }
    &:before {
      content: '';
      position: absolute;
      top: .5em;
      left: 0;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background-color: $theme-color;

    }
    &:last-child {
      margin-bottom: 0;
    }
    div{
      &:first-child {
        font-size: 18px;
        font-weight: 500;
        @media (max-width: 480px) {
          font-size: 16px;
        }
        & + * {
          margin-top: 15px;
          @media (max-width: 480px) {
            margin-top: 10px;
          }
        }
      }
    }
  }
}

.linked-list {
  position: relative;
  padding-left: 1px;

  li {
    display: flex;
    //margin-bottom: 30px;
    padding-bottom: 30px;
    padding-left: 37px;
    position: relative;
    &:before {
      content: '';
      width: 28px;
      height: 1px;
      background-color: rgba(255,255,255,.2);
      position: absolute;
      left: 0;
      top: 30px;
    }
    &:after {
      content: '';
      width: 1px;
      height: 100%;
      background-color: rgba(255,255,255,.2);
      position: absolute;
      left: -1px;
      top: 0;
    }
    &:first-child {

    }
    &:last-child {
      margin-bottom: 0;
      &:after {
        height: 30px;

      }
    }
    & > div {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
    }
    span {
      display: block;
      &:first-child {
        font-weight: 500;
        font-size: 18px;
      }
      &:nth-child(2) {
        margin-top: 15px;
        color: $text-color-light;
      }
    }
    img{
      width: 60px;
      margin-right: 22px;
    }
  }
}
