.settings-wrap {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
}

.settings-main {
  width: 850px;
  max-width: 100%;
  flex: 1 1 auto;
  @media (max-width: 1300px) {
    width: 700px;
  }
  .modal & {
    width: 610px;
    flex: 0 1 auto;
  }
  .profile-photo {
    background-color: $bg-color;
    border-radius: 10px;
    padding: 30px;
  }
  .form-section > .profile-photo {

    margin: 30px 0;
    flex-direction: row;
    @media (max-width: 640px) {
      flex-direction: column;
    }
    &__tools {
      @media (min-width: 721px) {
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
      }
    }
    &__add-btn {
      @media (min-width: 721px) {
        margin-bottom: 0;
        margin-right: 30px;
      }
    }
    &__ava {
      @media (min-width: 641px) {
        margin: 0 30px;
      }
    }
    @media (min-width: 1201px) {
      display: none;
    }
  }
  .input-row {
    @media (max-width: 768px) {
      flex-wrap: wrap;
    }
    &--3col {
      @media (max-width: 768px) {
        flex-wrap: nowrap;
      }
      @media (max-width: 480px) {
        flex-wrap: wrap;
      }
    }
    .input-wrap {
      @media (max-width: 768px) {
        flex: 1 1 calc(100% - 20px);
      }
    }
  }
}

.settings-sidebar {
  padding-left: 50px;
  padding-top: 50px;
  @media (max-width: 1200px) {
    display: none;
  }
  .modal & {
    flex: 1 0 auto;
  }
}

.settings-sidebar-section {
  background-color: $bg-color;
  border-radius: 10px;
  padding: 40px;
  margin-bottom: 30px;
  &:last-child {
    margin-bottom: 0;
  }
  .modal & {
    padding: 30px;
  }
}

.profile-photo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  &__label {
    font-size: 18px;
    font-weight: 500;
    text-align: center;
    margin-bottom: 20px;
  }
  &__ava {
    margin: 0 0 20px;
    border-radius: 50%;
  }
  &__media {
    margin: 20px 0;
    background-color: $theme-color-2;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    cursor: pointer;
    &:hover {
      .profile-photo__play {
        border-color: transparent transparent transparent $theme-color;
      }
    }
  }
  &__video-screen {
    opacity: .2;
  }
  &__play {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 8px 0 8px 10px;
    border-color: transparent transparent transparent #ffffff;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    transition: border-color .25s;
  }
  &__tools {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }
  &__add-btn {
    margin-bottom: 20px;
  }
}

.profile-media {
  display: flex;
  margin: 0 -15px 0;
  @media (min-width: 1201px) {
    display: none;
  }
  @media (max-width: 480px) {
    margin-top: -20px;
    flex-wrap: wrap;
  }
  .profile-photo {
    margin: 30px 15px 0;
    flex: 1 1 50%;
    @media (max-width: 480px) {
      flex: 1 1 100%;
    }
  }
}

.own-page {
  display: block;
  margin-top: 40px;
  a {
    color: $text-color-light;
    &:hover {
      color: $link-color-hover;
    }
  }
}
