.audio-progress{
  display: flex;
  align-items: flex-end;
  margin: 0 -3px;
  &__item{
    width: 6px;
    height: 50px;
    background-color: #D8D8D8;
    margin: 0 3px;
    border-radius: 3px;
    &._filled{
      background-color: $theme-color-4-light;
    }
    &._active{
      height: 60px;
      @media (max-width: 600px) {
        height: 40px;
      }
      @media (max-width: 400px) {
        height: 28px;
      }
    }
  }
}
