body.loaded {
  position: relative;
  overflow: hidden;
  &:before {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    background-color: #fff;
    background-image: url("../images/loader.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 150px auto;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 9999;
  }
}
