.filter {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: -20px -10px 0;
  @media (max-width: 540px) {
    //margin-top: -20px;
  }
  & + .filter {
    margin-top: 0;
  }
  &__item {
    width: calc(25% - 20px);
    margin: 40px 10px 0;
    @media (max-width: 1200px) {
      width: calc(50% - 20px);
    }
    @media (max-width: 540px) {
      width: calc(100% - 20px);
      margin-top: 20px;
    }
    &--desktop {
      @media (max-width: 540px) {
        display: none;
      }
    }
    &--shadow {
      opacity: 0;
      @media (max-width: 540px) {
        display: none;
      }
    }
  }
  &__label {
    display: block;
    width: 100%;
    text-align: center;
    font-weight: 500;
    font-size: 18px;
    margin-bottom: 20px;
    @media (max-width: 540px) {
      margin-bottom: 10px;
      font-size: 16px;
    }
  }
  &--mob {
    margin-top: -10px;
    margin-bottom: 20px;
    @media (min-width: 541px) {
      display: none;
    }
  }
  &--mob-holder {
    @media (max-width: 540px) {
      margin-top: -20px;
      margin-bottom: 20px;
    }
  }
  &.hidden-content {
    @media (max-width: 540px) {
      display: flex;
    }

    //display: flex!important;
  }
  &--single {
    @media (min-width: 541px) {
      position: absolute;
    }
    @media (max-width: 540px) {
      order: 1;
      width: 100%;
      margin: 0 0 20px;
    }
    .filter__item {
      @media (max-width: 540px) {
        width: 100%;
        margin: 0;
      }
    }
  }
}

.filter-tools{
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-top: 10px;
  margin-bottom: 24px;
  @media (max-width: 540px) {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }
  .toggle-hidden-btn{
    font-size: 14px;
    @media (max-width: 540px) {
      display: none;
    }
  }
}

.sort {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  //margin-top: -56px;
  //padding-bottom: 30px;

  @media (max-width: 540px) {
    margin-top: 10px;
    padding-bottom: 12px;
    justify-content: center;
  }
}

.price-select .ss-content .ss-list .ss-option {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.price-select.ss-main .ss-single-selected .placeholder {
  justify-content: space-between;
  padding-right: 10px;
}

.filter-open-btn {
  @extend .secondary-btn;
  @extend .sm-btn;
  width: 100%;
  height: 38px;
  font-weight: 500;
  margin-bottom: 30px;
  @media (min-width: 541px) {
    display: none;
  }
  @media (max-width: 540px) {
    order: 2;
  }
  .svg-icon {
    fill: $theme-color;
  }
}

.filter-close-btn {
  @extend .btn;
  position: absolute;
  right: 20px;
  top: 20px;
  @media (min-width: 541px) {
    display: none;
  }
  .svg-icon {
    fill: $text-color-base;
  }
}

.filter-panel {
  @media (max-width: 540px) {
    position: fixed;
    height: 100%;
    top: 0;
    left: 0;
    width: 100vw;
    background-color: #ffffff;
    z-index: 10;
    padding: 60px 20px 20px;
    transform: translateX(-110%);
    transition: transform .25s;
    overflow: auto;
  }
  &._visible {
    transform: translateX(0);
  }
  .modal {
    @media (max-width: 540px) {
      width: calc(100% - 40px);
    }
    &._opened {
      @media (max-width: 540px) {
        width: 100vw;
      }
    }
  }
}

.filtered-count {
  display: none;
  align-items: center;
  justify-content: center;
  background-color: $theme-color;
  color: #ffffff;
  border-radius: 50%;
  margin-left: 10px;
  font-size: 11px;
  width: 22px;
  height: 22px;
  &._visible {
    display: inline-flex;
  }
}
