.miniplayer-btn {
  @extend .btn;
  width: 50px;
  height: 34px;
  border: 1px solid #FFFFFF;
  border-radius: 6px;
  position: relative;
  &:after {
    content: '';
    display: inline-block;
    background: #FFFFFF;
    border-radius: 4px;
    right: 6px;
    bottom: 6px;
    position: absolute;
    width: 22px;
    height: 16px;
  }
}

.conf {
  padding: 40px;
  background-color: $theme-color-2;
  width: 100%;
  height: 100vh;
  position: relative;
  @media (max-width: 600px) {
    padding: 20px;
  }
  &__inner {
    display: flex;
    height: 100%;
  }
  &__main {
    flex: 1 1 auto;
  }
  &__sidebar {
    width: 230px;
    margin-left: 42px;
    flex: 0 0 auto;
    @media (max-width: 600px) {
      width: 100px;
      margin-left: 20px;
    }
    @media (max-width: 400px) {
      width: 60px;
    }
  }
  &__user {
    margin-bottom: 30px;
    @media (max-width: 600px) {
      margin-bottom: 20px;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__miniplayer-btn {
    position: absolute;
    right: 40px;
    bottom: 40px;
    @media (max-width: 600px) {
      right: 20px;
      bottom: 20px;
    }
  }
  &__search-frm {
    position: relative;
    height: 50px;
  }
  &__search-submit {
    height: 30px;
    padding: 0 20px;
    border-radius: 15px;
    font-size: 14px;
    position: absolute;
    top: 10px;
    right: 10px;
    min-width: unset;
    @media (max-width: 600px) {
      height: 26px;
      top: 5px;
      right: 5px;
    }
  }
}

input.conf__search-input {
  height: 50px;
  border-color: $border-color;
  border-radius: 25px;
  color: #ffffff;
  @media (max-width: 600px) {
    height: 36px;
    border-radius: 18px;
  }
}


