.testimonial {
  padding: 28px 40px 33px;
  background-color: #fff;
  border-radius: 12px 12px 10px 10px;
  @media (max-width: 480px) {
    padding: 20px;
  }
  &__top {
    display: flex;
    align-items: center;
    padding-bottom: 20px;
    margin-bottom: 30px;
    border-bottom: 1px solid $border-color;
    @media (max-width: 480px) {
      display: block;
      margin-bottom: 20px;
    }
  }
  &__customer {
    display: flex;
    align-items: center;
  }
  &__ava {
    margin-right: 14px;
    @media (max-width: 480px) {
      float: left;
    }
    img{
      border-radius: 50%;
      display: block;
    }
  }
  &__name {
    font-size: 18px;
    font-weight: 500;
    padding-top: 5px;
  }
  &__rating {
    margin-left: auto;
    @media (max-width: 480px) {
      margin: 6px 0 0 -1px;
      position: relative;
      left: -2px;
    }
  }
  &__body {
    @media (max-width: 480px) {
      font-size: 12px;
      line-height: 1.185;
    }
  }
  &__footer {
    margin-top: 20px;
    color: $text-color-light;
    @media (max-width: 480px) {
      font-size: 12px;
      margin-top: 10px;
    }
  }
}

.testimonials-slider-wrap {
  @media (max-width: 768px) {
    margin-right: -40px;
  }
  .slider-nav {
    @media (max-width: 768px) {
      display: none;
    }
  }
}

.testimonials-slider {
  &__item {
    width: calc(50% - 10px);
    min-height: 100%;
    margin: 0 10px;
    @media (max-width: 768px) {
      width: 610px;
    }
    @media (max-width: 480px) {
      width: 240px;
    }
  }
  .flickity-slider {
    //margin: 0 -10px;
  }
}
