.features {
  display: flex;
  justify-content: center;
  margin: 0 -40px;
  @media (max-width: 600px) {
    margin: 0 -20px;
  }
  @media (max-width: 540px) {
    flex-wrap: wrap;
    margin: -25px 0 0;
  }
  &__item {
    width: 227px;
    margin: 0 40px;
    position: relative;
    @media (max-width: 600px) {
      width: calc(33.3333 - 40px);
      margin: 0 20px;
    }
    @media (max-width: 540px) {
      width: 100%;
      margin: 25px 0 0;
    }
    &:last-child {
      &:after {
        display: none;
      }
    }
    &:after {
      content: '';
      display: block;
      width: 159px;
      height: 1px;
      background-color: #979797;
      position: absolute;
      top: 45px;
      right: -120px;
      @media (max-width: 1024px) {
        width: 83px;
        right: -75px;
      }
      @media (max-width: 600px) {
        display: none;
      }
    }
    &--wide {
      width: 300px;
    }
  }
}

.feature {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  &__icon-wrap {
    height: 90px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
    @media (max-width: 540px) {
      margin-bottom: 20px;
      height: auto;
      width: 60px;
    }
  }
  &__title {
    font-size: 18px;
    font-weight: 500;
    line-height: 1.185;
    margin-bottom: 15px;
    text-align: center;
  }
  &__desc {
    font-size: 14px;
    line-height: 1.25;
    color: $text-color-light;
    text-align: center;
  }
}
