.cmplx-input {
  position: relative;
  &:before {
    content: 'Choose';
    //color: #ffffff;
    width: 76px;
    height: 30px;
    margin: 0 0 0 10px;
    border-radius: 18px;
    background-color: $theme-color;
    position: absolute;
    right: 5px;
    top: 5px;
    color: #ffffff;
    transition: background-color .25s;
    font-size: 14px;
    line-height: 1;
    text-align: center;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    z-index: 2;
  }
  &--lg {
    &:before {
      top: 10px;
      right: 10px;
    }
  }
  &._opened {
    &:before {
      z-index: 7;
      background-color: $text-color-light;
    }
  }
  &.has-overlay-input {
    &._opened {
      &:after {
        content: '';
        display: block;
        position: fixed;
        width: 100%;
        height: 100%;
        background-color: rgba(255,255,255,.4);
        top: 0;
        left: 0;
        z-index: 6;
        cursor: default;
      }
    }
  }
  &.has-filter {

  }
  &__dropdown {
    width: 100%;
    //padding: 15px 30px 30px;
    padding: 10px 30px 30px;
    background-color: #fff;
    border-radius: 0 0 20px 20px;
    border: 1px solid $inputs-border-color;
    border-top: 0;
    //margin-top: -15px;
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    @media (max-width: 600px) {
      padding: 10px 20px 20px;
    }
    ._opened > &{
      display: block;
      z-index: 7;
    }
    input {
      //height: 40px;
      height: 38px;
      padding: 0 30px;
      border-radius: 18px;
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    .ss-main {
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    .check-wrap {
      & + .primary-btn {
        margin-top: 20px;
      }
    }

    .selectize-control {
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
      & + .primary-btn {
        margin-top: 10px;
      }
    }
  }
  //&__dropdown-btns {
  //  display: flex;
  //  align-items: center;
  //  margin: -10px -5px;
  //  &  > * {
  //    margin: 10px 5px 0;
  //  }
  //}
}

input.cmplx-input__input {
  height: 40px;
  padding: 0 85px 0 30px;
  border-radius: 18px;
  position: relative;
  z-index: 2;
  transition: none;
  cursor: pointer;
  text-overflow: ellipsis;
  @media (max-width: 600px) {
    font-size: 14px;
    padding-left: 20px;
  }
  .cmplx-input--lg &{
    height: 50px;
    border-radius: 25px;
    @media (max-width: 600px) {
      height: 40px;
      border-radius: 20px;
      padding-left: 20px;
      font-size: 14px;
    }
  }
  &:hover,
  &:focus{
    border-color: $inputs-border-color;
  }
  ._opened > & {
    border-bottom: 0;
    border-radius: 18px 18px 0 0;
    z-index: 7;
    display: block;
  }

  .has-filter & {
    &--filter {
      display: none;
    }
  }
  .has-filter._opened & {
    display: none;
    &--filter {
      display: block;
    }
  }
  &--filter {

  }
}

.cmplx-select {
  .selectize-input {
    padding: 0 85px 0 30px;
    @media (max-width: 600px) {
      padding: 0 85px 0 20px;
    }
  }
}
