.confirmed {
  display: inline-flex;
  align-items: center;
  line-height: 1;
  color: $theme-color-4-light;
  padding-left: 16px;
  background-image: url("../images/tick-green.svg");
  background-position: left center;
  background-repeat: no-repeat;
}

.unconfirmed {
  @extend .confirmed;
  padding-left: 19px;
  color: $text-color-light;
  background-image: url("../images/close-grey.svg");
}
