.rate{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
  height: 36px;
  border-radius: 18px;
  background-color: $bg-color;
  color: $text-color-base;
  padding: 0 20px;
  &--lg {
    height: 50px;
    font-size: 16px;
    min-width: 200px;
    border-radius: 25px;
  }
}

.rate-level {
  line-height: 14px;
  &--lg {
    img {
      width: 65px;
      height: 15px;
    }
  }
}
