*,
:before,
:after {
  box-sizing: border-box;
}

html {
  font-family: $ff-base;
  text-size-adjust:100%;
  display: flex;
  align-items: flex-start;
  min-height: 100%;
  color: $text-color-base;
  background: #ffffff;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  font-family: $ff-base;
  font-size: $fs-base;
  line-height: $line-height;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  align-items: center;
  @media (max-width: 400px) {
    font-size: 12px;
  }
}

h1,.h1 {
  font-family: $ff-base;
  font-size: $fs-h1;
  font-weight: 500;
  line-height: 1.185;
  margin: 0 0 50px;
  @media (max-width: 480px) {
    font-size: 24px;
    margin-bottom: 20px;
  }
}

h2,.h2 {
  font-family: $ff-base;
  font-size: $fs-h2;
  font-weight: 500;
  line-height: 1.185;
  margin: 0 0 24px;
  @media (max-width: 600px) {
    font-size: 24px;
  }
}

h3,.h3 {
  font-family: $ff-base;
  font-size: $fs-h3;
  font-weight: 500;
  line-height: 1.185;
  margin: 0;
}

h4,.h4 {
  font-family: $ff-base;
  font-size: $fs-h4;
  font-weight: 700;
  line-height: 1.185;
  margin: 0;
}

a{
  color: $link-color;
  text-decoration: none;
  transition: color .25s;
  &:hover{
    color: $link-color-hover;
    .icon{
      fill: $link-color-hover;
    }
  }
  &:focus{
    outline: none;
  }
  &:active{
    outline: none;
  }
  .icon{
    fill: $link-color;
  }
}
a[href^="tel:"],
a[href^="callto:"]{
  color: inherit;
  text-decoration: none;
}

b, strong{
  font-weight: 700;
}

img {
  vertical-align: middle;
  max-width: 100%;
  height: auto;

}

p{
  margin-bottom: 1.35em;
  margin-top: 1em;
  &:first-child{
    margin-top: 0;
  }
  &:last-child{
    margin-bottom: 0;
  }
}

table{
  width: 100%;
  margin-top: 40px;
  margin-bottom: 40px;
  border-collapse: collapse;
  td,th{}
  thead{
    th,td {
      padding: 0 10px 20px;
      font-size: 14px;
      font-weight: 400;
      color: $text-color-light;
      text-align: left;
      &:first-child {
        padding-left: 30px;
      }
      &:last-child {
        padding-right: 30px;
      }
    }
  }
  tbody{
    tr {
      &:first-child {
        td {
          &:first-child {
            border-radius: 12px 0 0 0;
          }
          &:last-child {
            border-radius: 0 12px 0 0;
          }
        }
      }
      &:last-child {
        td {
          &:first-child {
            border-radius: 0 0 0 10px;
          }
          &:last-child {
            border-radius: 0 0 10px 0;
          }
        }
      }
    }
    td{
      padding: 8px 10px;
      font-size: 14px;
      background-color: #fff;
      &:first-child {
        padding-left: 30px;
      }
      &:last-child {
        padding-right: 30px;
      }
    }
  }
  tfoot{}
}

blockquote{}

input[type="email"], input[type="number"], input[type="password"], input[type="search"],
input[type="tel"], input[type="text"], input[type="url"], input[type="color"],
input[type="date"], input[type="datetime"], input[type="datetime-local"],
input[type="month"], input[type="time"], input[type="week"], textarea {
  width: 100%;
  height: 60px;
  appearance: none;
  outline: 0;
  font-size: 14px;
  line-height: normal;
  font-weight: 400;
  border-radius: 30px;
  border: 1px solid $inputs-border-color;
  background-color: transparent;
  padding: 0 30px;
  transition: border-color .3s;
  @media (max-width: 600px) {
    height: 44px;
    font-size: 12px;
    padding: 0 22px;
    border-radius: 23px;
  }
  &.md-input {
    height: 50px;
    border-radius: 25px;
    @media (max-width: 600px) {
      height: 40px;
      border-radius: 20px;
      padding: 0 20px;
      font-size: 14px;
    }
  }
  &:hover{
    border-color: $inputs-focus-border-color;
  }
  &:focus{
    outline: none;
    border-color: $inputs-focus-border-color;
  }
}

@include input-placeholder {
  color: $text-color-light;
  opacity: 1;
  transition: opacity 0.3s ease;
}
:focus::-webkit-input-placeholder {
  opacity: 0;
}
:focus::-moz-placeholder {
  opacity: 0;
}
:focus:-moz-placeholder {
  opacity: 0;
}
:focus:-ms-input-placeholder {
  opacity: 0;
}

/*For FF*/
input:required {
  box-shadow:none;
}

textarea{
  height: 160px;
  line-height: 1.6em;
  padding-top: 1.5em;
  padding-bottom: 1em;
  resize: vertical;
}

img {
  vertical-align: middle;
  max-width: 100%;
}

