.rating {
  height: 30px;
  padding: 0 14px;
  background-color: $theme-color-3;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  line-height: 1;
  border-radius: 18px;
  color: $text-color-base;
  flex: 0 0 auto;
  @media (max-width: 640px) {
    font-size: 12px;
    height: 24px;
    padding: 0 10px;
    border-radius: 12px;
  }
}

.mark{
  display: flex;
  align-items: center;
  margin: 0 -4px;
  @media (max-width: 480px) {
    margin: 0 -3px;
  }
  &__item{
    display: inline-block;
    width: 16px;
    height: 15px;
    margin: 0 4px;
    background-image: url('../images/star.svg');
    background-repeat: no-repeat;
    @media (max-width: 480px) {
      width: 13px;
      height: 12px;
      background-size: cover;
      margin: 0 3px;
    }
    &--filled{
      background-image: url('../images/star-filled.svg');
    }
  }
}

.active-rating{
  font-size: 0;
  display: flex;
  align-items: center;
  &__item{
    width: 25px;
    height: 24px;
    position: relative;
    flex-shrink: 0;
    margin-right: 4px;
    background-color: transparent;
    &:last-child{
      margin-right: 0;
    }
    &:hover{
      .icon{
        transform: scale(1.15);
      }
    }
    &.is-selected{
      .svg-icon{
        fill: $theme-color-3;
      }
    }
    &.is-filled{
      .svg-icon{
        fill: $theme-color-3;
      }
    }
    .svg-icon{
      position: absolute;
      left: 0;
      top: 0;
      display: inline-block;
      fill: $text-color-ultralight;
    }
  }
}

.active-rating-text {
  display: none;
  margin-left: 30px;
  line-height: 1.2;
  font-size: 17px;
  @media (max-width: 480px) {
    margin-left: 15px;
    font-size: 14px;
  }
  @media (max-width: 360px) {
    margin-left: 10px;
    font-size: 12px;
  }
  span {
    color: $theme-color;
    font-weight: 500;
    @media (max-width: 360px) {
      font-size: 14px;

    }
  }
  &._visible {
    display: block;
    @media (max-width: 360px) {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
    }
  }
}

.active-rating-wrap {
  display: flex;
  align-items: center;
  @media (max-width: 480px) {
    height: 28px;
  }
}


