.schedule {
  position: relative;
  &__header {
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 11px;
    justify-content: center;
    position: relative;
    @media (max-width: 740px) {
      flex-wrap: wrap;
    }
    @media (max-width: 460px) {
      margin-bottom: 0;
    }
    .schedule__prev,
    .schedule__next {
      @media (max-width: 640px) {
        display: none;
      }
    }
  }
  &__title {
    text-align: center;
    font-size: 18px;
    font-weight: 500;
    line-height: 1;
    margin: 0 auto;
    @media (max-width: 740px) {
      width: 100%;
      order: 0;
      margin-bottom: 16px;
    }
  }
  &__prev {
    @media (max-width: 740px) {
      margin-right: 30px;
      order: 1;
    }
  }
  &__next {
    @media (max-width: 740px) {
      margin-left: 10px;
      order: 4;
    }
  }
  &__mob-nav {
    display: flex;
    margin: 20px -10px 0;
    @media (min-width: 641px) {
      display: none;
    }
    .slider-nav-btn {
      flex: 1 1 auto;
      margin: 0 10px;
      border-radius: 25px;
    }
  }
  &__toggle {
    @media (min-width: 741px) {
      position: absolute;
      left: 65px;
      top: 10px;
    }
    @media (max-width: 740px) {
      order: 2;
      margin-right: auto;
      margin-top: 3px;
    }
    @media (max-width: 460px) {
      width: 126px;
      margin-top: 7px;
    }
    input[type=checkbox].switch-checkbox+label{
      @media (max-width: 460px) {
        font-size: 12px;
      }
    }
  }
  &__timezone {
    flex: 0 0;
    @media (min-width: 741px) {
      position: absolute;
      right: 47px;
      top: -3px;
    }
    @media (max-width: 740px) {
      order: 3;
      margin-left: auto;
    }

    .select-wrap {
      @media (max-width: 460px) {
        flex-wrap: wrap;
      }
      label {
        @media (max-width: 460px) {
          position: relative;
          top: 14px;
          font-size: 12px;
        }
      }
      .simple-select.ss-main {
        @media (max-width: 460px) {
          font-size: 12px;
        }
        .ss-content {
          .ss-list {
            @media (max-width: 460px) {
              padding: 0 18px 8px;
            }
            .ss-option {
              @media (max-width: 460px) {
                padding: 0;
                font-size: 12px;
                height: auto;
                margin-bottom: 5px;
              }
            }
          }
        }

      }

    }
  }
  &__calendar {
    display: flex;
    padding: 0 34px 0 0;
    border-radius: 12px 12px 10px 10px;
    position: relative;
    @media (max-width: 740px) {
      padding-right: 20px;
    }
    &:before {
      content: '';
      display: block;
      width: 34px;
      height: 60px;
      border-radius: 0 12px 12px 0;
      background-color: $theme-color-2;
      position: absolute;
      right: 0;
      top: 9px;
      @media (max-width: 740px) {
        width: 20px;
      }
      @media (max-width: 480px) {
        height: 50px;
        top: 8px;
      }
    }
    &:after {
      content: '';
      display: block;
      width: 35px;
      height: calc(100% - 79px);
      border-radius: 0 12px 12px 0;
      background-color: $bg-color;
      position: absolute;
      right: 0;
      bottom: 0;
      @media (max-width: 740px) {
        width: 20px;
      }
      @media (max-width: 480px) {
        height: calc(100% - 68px);
      }
    }
  }
  &__time {
    width: 90px;
    flex: 0 0 90px;
    padding-top: 9px;
    @media (max-width: 740px) {
      width: 72px;
      flex: 0 0 72px;
    }
    @media (max-width: 480px) {
      padding-top: 8px;
    }
  }
  &__time-header {
    height: 60px;
    background-color: $theme-color-2;
    margin-bottom: 10px;
    border-radius: 12px 0 0 12px;
    @media (max-width: 480px) {
      height: 50px;
    }
  }
  &__times {
    padding: 7px 27px 26px;
    border-radius: 12px 0 0 12px;
    background-color: $bg-color;
    @media (max-width: 740px) {
      padding: 7px 0 26px 18px;
    }
    @media (max-width: 480px) {
      padding-top: 2px;
    }
  }
  &__time-value {
    height: 36px;
    display: flex;
    align-items: flex-end;
    line-height: 1;
    color: $text-color-light;
    @media (max-width: 480px) {
      font-size: 12px;
    }
    &:nth-child(9) {
      margin-bottom: 0;
    }
    &._hidden {
      display: none;
      .schedule._expanded & {
        display: flex;
      }
    }
  }
  &__days {
    flex: 1 1 auto;
    position: relative;
    &:after {
      content: '';
      display: block;
      width: 100%;
      height: 36px;
      background-color: $bg-color;
      position: absolute;
      bottom: 0;
      left: 0;
      border-top: 1px solid $border-color;
    }
  }
  &__day {
    width: calc(100% / 7);
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
    padding-top: 9px;
    @media (max-width: 480px) {
      width: calc(100% / 4);
      padding-top: 8px;
    }
    //@media (max-width: 360px) {
    //  width: calc(100% / 2);
    //}
    &._current {
      position: relative;
      &:before {
        content: 'now';
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 36px;
        height: 18px;
        border-radius: 9px;
        background-color: $theme-color-3;
        font-size: 12px;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        margin: auto;
        @media (max-width: 480px) {
          font-size: 10px;
          height: 16px;
          width: 30px;
        }
      }
    }
  }
  &__day-header {
    width: calc(100% + 1px);
    height: 60px;
    background-color: $theme-color-2;
    color: #ffffff;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-bottom: 10px solid #ffffff;
    box-sizing: content-box;
    @media (max-width: 480px) {
      font-size: 12px;
      height: 50px;
    }
    ._current > & {
      background-color: #444444;
    }
  }
  &__booking-times {
    background-color: $bg-color;
    padding: 34px 0 0;
    border-bottom: 1px solid $border-color;
    @media (max-width: 480px) {
      padding-top: 30px;
    }
    .schedule._expanded & {
      border-bottom: 0;
    }

  }
  &__booking-time {
    width: calc(100% + 1px);
    height: 36px;
    border: 1px solid $border-color;
    border-right: 0;
    border-bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
    position: relative;
    & > span{
      //display: none;
      opacity: 0;

    }
    &:last-child {
      border-bottom: 1px solid $border-color;
      &.schedule__booking-time--free {
        border-bottom-color: $theme-color-4;
      }
    }
    &--free {
      background-color: $theme-color-4-light;
      color: #ffffff;
      position: relative;
      cursor: pointer;
      &:before {
        content: '';
        display: block;
        width: calc(100% + 2px);
        height: calc(100% + 2px);
        position: absolute;
        left: -1px;
        top: -1px;
        border: 1px solid $theme-color-4;
      }
      span {
        display: inline-block;
        opacity: 1;
      }
    }
    &._hidden {
      display: none;
      .schedule._expanded & {
        display: flex;
      }
    }
    &._active {
      background-color: #317ec7;
      color: #ffffff;
      cursor: pointer;
      &:before {
        content: '';
        display: block;
        width: calc(100% + 2px);
        height: calc(100% + 2px);
        position: absolute;
        left: -1px;
        top: -1px;
        border: 1px solid #236bb0;
      }
      &:hover {
        background-color: #317ec7;
      }
      & > span {
        display: inline-block;
        opacity: 1;
      }
    }
    &._pending {
      position: relative;
      overflow: hidden;
      &:after {
        content: '';
        position: absolute;
        left: -46px;
        right: 0;
        top: 0;
        bottom: 0;
        display: block;
        background: repeating-linear-gradient(
                        -55deg
                , #2393d8 1px, #43b0f3 2px, #43b0f3 11px, #2393d8 12px, #2393d8 20px);
        animation-name: move-bg;
        animation-duration: .6s;
        animation-timing-function: linear;
        animation-iteration-count: infinite;
      }
      //pointer-events: none;
    }
    &._disabled {
      background-color: $border-color;
      pointer-events: none;
      position: relative;
      &:before {
        content: '';
        display: inline-block;
        width: 16px;
        height: 100%;
        background-image: url("../images/lock.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 16px auto;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        opacity: .6;
      }
    }
    &._free {
      cursor: pointer;
    }
    &._ready{
      background-color: $theme-color-4-light;
      cursor: pointer;
    }
    ._pending & {
      cursor: pointer;
    }
    span:not(.tooltip) {
      //position: relative;
      //z-index: 1;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      line-height: 1;
    }
  }
  &:before {
    content: '';
    display: block;
    width: 1px;
    height: calc(100% - 200px);
    background-color: $border-color;
    position: absolute;
    right: 35px;
    bottom: 35px;
    z-index: 1;
    @media (max-width: 740px) {
      right: 20px;
      height: calc(100% - 237px);
    }
    @media (max-width: 640px) {
      bottom: 95px;
      height: calc(100% - 298px);
    }
    @media (max-width: 480px) {
      bottom: 95px;
      height: calc(100% - 282px);
    }
    @media (max-width: 460px) {
      bottom: 95px;
      height: calc(100% - 292px);
    }
  }
  &:after {
    content: '';
    display: block;
    width: 1px;
    height: 287px;
    background-color: $border-color;
    position: absolute;
    left: 90px;
    bottom: 35px;
    z-index: 1;
    @media (max-width: 740px) {
      left: 72px;
    }
    @media (max-width: 640px) {
      bottom: 95px;
    }
  }
  .flickity-viewport {

  }
}

.schedule-top-title {
  font-size: 38px;
  font-weight: 500;
  text-align: center;
  margin-bottom: 30px;
}

._pending .schedule__booking-time:not(._active) {
  &:hover {
    background-color: #fff5d7;
  }
}

._pending .schedule__booking-time._active:not(._pending) {
  pointer-events: none;
}

.modal .schedule-notice {
  @media (min-width: 601px) {
    margin-top: -50px;
  }
}

.schedule-change-tools {
  border: 1px solid $theme-color-4-light;
  border-radius: 10px;
  padding: 15px 10px 10px;
  margin-bottom: 20px;
  display: none;
  &._visible {
    display: block;
  }
}
.schedule-change-type {
  justify-content: center;
  margin: -20px -20px 20px;
  .check-wrap {
    margin: 20px 20px 0;
  }
}
.schedule-change-info {
  text-align: center;
  //font-weight: 500;
  font-size: 18px;
  margin-bottom: 20px;

  color: $theme-color-4-light;
  p {
    margin-bottom: 0;
    margin-top: 10px;
  }
}

.schedule-note {
  font-size: 14px;
  color: $text-color-light;
  margin-top: 20px;
}

.schedule-notices {
  display: flex;
  justify-content: center;
  margin: -30px -15px 30px;
  @media (max-width: 400px) {
    margin: -15px -15px 20px;
    flex-wrap: wrap;
  }
  .schedule-notice {
    margin: 15px 15px 0;
    @media (max-width: 400px) {
      width: calc(100% - 30px);
    }
  }
}
