.person {
  display: inline-flex;
  align-items: center;
  &__ava {
    border-radius: 50%;
    width: 30px;
    height: 30px;
    position: relative;
    z-index: 1;
  }
  &__ava-wrap {
    width: 30px;
    height: 30px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    &--empty {
      border: 1px solid #9E9FA1;
      background-image: url("../images/user.svg");
      background-position: center;
      background-repeat: no-repeat;
    }
  }
  &__name {
    margin-left: 10px;
    font-size: 14px;
    line-height: 1;
  }
}
