.guarantee {
  font-size: 16px;
  font-weight: 500;
  display: flex;
  align-items: center;
  img {
    margin-right: 14px;
  }
  div {
    span {
      display: block;
      &:nth-child(2){
        font-size: 14px;
        font-weight: 400;
        margin-top: 15px;
        color: $text-color-light;
      }
    }
  }
  &--lg {
    img {
      margin-right: 25px;
    }
  }
}
