.page-top {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  text-align: center;
  width: 620px;
  max-width: 100%;
  margin: 60px auto 0;
  @media (max-width: 480px) {
    margin-top: 40px;
  }
  &__img {
    margin-bottom: 30px;
    @media (max-width: 480px) {
      margin-bottom: 20px;
      width: 100px;
    }
  }
  &__title {
    font-size: 44px;
    font-weight: 500;
    line-height: 1.2;
    @media (max-width: 480px) {
      font-size: 30px;
    }
  }
  &__intro {
    margin-top: 30px;
    font-size: 18px;
    line-height: 1.185;
    @media (max-width: 480px) {
      font-size: 16px;
      margin-top: 20px;
    }
  }
}
