.find-tutor {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  &__title {
    font-size: 44px;
    font-weight: 500;
    margin-bottom: 40px;
    @media (max-width: 750px) {
      font-size: 30px;
    }
  }
  &__guarantee {

  }
}

.guarantee-block {
  width: 1030px;
  max-width: 100%;
  margin: 80px auto 0;
  display: flex;
  align-items: center;
  font-size: 18px;
  font-weight: 500;
  @media (max-width: 750px) {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    font-size: 16px;
    margin-top: 30px;
  }
  div{
    &:first-child {
      @media (max-width: 768px) {
        width: 220px;
      }
      @media (max-width: 750px) {
        text-align: center;
        width: auto;
        margin-bottom: 60px;
      }
    }
  }
  .guarantee {
    margin-left: 148px;
    position: relative;
    @media (max-width: 768px) {
      margin-left: 100px;
    }
    @media (max-width: 750px) {
      margin: 0;
      br {
        display: none;
      }
    }
    @media (max-width: 480px) {
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      text-align: center;
    }
    img {
      @media (max-width: 480px) {
        margin: 0 0 20px;
      }
    }
    &:before {
      content: '';
      display: block;
      width: 66px;
      height: 7px;
      background-image: url('../images/arrow.svg');
      background-repeat: no-repeat;
      background-position: right center;
      position: absolute;
      left: -100px;
      top: 68px;
      @media (max-width: 768px) {
        width: 48px;
        left: -80px;
      }
      @media (max-width: 750px) {
        width: 30px;
        transform: rotate(90deg);
        left: 0;
        right: 0;
        margin: auto;
        top: 0;
        margin-top: -34px;
      }
    }
  }
}
