.dashboard {
  display: flex;
  flex-wrap: wrap;
  margin: -20px -10px 0;
  &__section {
    width: calc(50% - 20px);
    margin: 20px 10px 0;
    @media (max-width: 1100px) {
      width: calc(100% - 20px);
    }
    .screen {
      &__body:not(.screen__body--no-pad) {
        @media (max-width: 480px) {
          padding: 20px;
        }
      }
    }
    &--wide {
      width: calc(100% - 20px);
    }
    &--lg {
      width: 715px;
      @media (max-width: 1200px) {
        width: 100%;
      }
      & + .dashboard__section {
        width: calc(100% - 715px - 40px);
        @media (max-width: 1200px) {
          width: 100%;
        }
      }
    }
    .info-block{
      height: 100%;
    }
  }
}

.dashboard-schedule {
  display: flex;
  align-items: flex-start;
  position: relative;
  margin-bottom: 30px;
  &__icon-wrap {
    margin-right: 24px;
    @media (max-width: 480px) {
      margin-right: 10px;
      .svg-icon {
        width: 40px;
        height: 40px;
      }
    }
  }
  &__info{}
  &__next-class {
    font-size: 24px;
    font-weight: 500;
    line-height: 1.2;
    @media (max-width: 480px) {
      font-size: 16px;
    }
    span {
      display: block;
      color: $theme-color;
    }
  }
  &__schedule {
    font-size: 16px;
    margin-top: 20px;
    @media (max-width: 480px) {
      font-size: 12px;
      margin-top: 10px;
    }
    & > div {
      &:first-child {
        color: $text-color-light;
      }
      &:nth-child(2) {
        margin-top: 5px;
        @media (max-width: 480px) {
          margin-top: 0;
        }
      }
    }
  }
}

.dashboard-schedule-btn {
  @extend .btn;
  font-size: 14px;
  position: absolute;
  right: 30px;
  bottom: 30px;
  color: $text-color-light;
  @media (max-width: 480px) {
    font-size: 12px;
    right: 20px;
    bottom: 20px;
  }
  &:hover {
    color: $text-color-base;
  }
}

.enter-classroom {
  background-image: url(../images/earth-sm.png);
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  @media (max-width: 1100px) {
    height: 258px;
  }
  @media (max-width: 480px) {
    height: 180px;
    background-size: auto 90%;
  }
}

.schedule-notice {
  text-align: center;
  margin-bottom: 30px;
  @media (max-width: 480px) {
    margin-bottom: 10px;
  }
  & > div {
    &:first-child {
      color: $text-color-light;
    }
    &:nth-child(2) {
      margin-top: 5px;
      @media (max-width: 480px) {
        line-height: 1.3;
        margin-top: 2px;
      }
    }
  }
}
