.promo {
  height: 600px;
  background-color: $theme-color-2;
  color: #ffffff;
  position: relative;
  @media (max-width: 600px) {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    height: auto;
  }
  &__inner {
    height: 100%;
    @extend .container;
    display: flex;
    @media (max-width: 600px) {
      order: 1;
      width: 100%;
      height: auto;
      padding-top: 40px;
      padding-bottom: 40px;
      display: block;
    }
    .promo--right & {
      justify-content: flex-end;
      @media (max-width: 600px) {
        padding-bottom: 28px;
      }
    }
  }
  &__text {

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

    @media (min-width: 601px) {
      width: 50%;
      padding-right: 30px;
    }
    @media (max-width: 600px) {
      align-items: center;
    }
    .promo--right & {
      padding-left: 115px;
      padding-right: 115px;
      @media (max-width: 1600px) {
        padding-right: 0;
        padding-left: 60px;
      }
      @media (max-width: 600px) {
        padding-left: 0;
        align-items: flex-start;
      }
    }
  }
  &__title {
    font-size: 36px;
    font-weight: 500;
    line-height: 1.185;
    margin-bottom: 40px;
    @media (max-width: 600px) {
      text-align: center;
      font-size: 30px;
    }
    .promo--top & {
      font-size: 44px;
      line-height: 1.185;
      @media (max-width: 600px) {
        font-size: 30px;
      }
    }
  }
  &__img-wrap{

    @media (min-width: 601px) {
      position: absolute;
      right: 0;
      top: 0;
      width: 50%;
      height: 100%;
      overflow: hidden;
    }
    .promo--right & {
      right: auto;
      left: 0;
      @media (max-width: 600px) {
        order: 1;
      }
    }
  }
  &__img {
    @media (min-width: 601px) {
      position: absolute;
      left: 0;
      top: 0;
      max-width: none;
    }
  }
  &--top {
    margin-top: -76px;
    z-index: 2;
    width: 100%;
    @media (max-width: 600px) {
      margin-top: 0;
    }
    img {
      @media (max-width: 1400px) {
        left: -280px;
      }
    }
  }
  &--right {
    img {
      @media (max-width: 1400px) {
        left: -180px;
      }
    }
  }
}
