input[type=checkbox],
input[type=radio   ]{
  margin    : 0;
  padding   : 0;
  font-size : 0;
  opacity   : 0;
  display: none;
}
input[type=checkbox] + label,
input[type=radio   ] + label{
  display: inline-flex;
  line-height: 1.3;
  font-size: 14px;
  font-weight: 400;
  cursor: pointer;
  min-height: 22px;
  position: relative;
  padding-left: 36px;
  align-items: baseline;
  transition: color .25s;
  @media (max-width: 480px) {
    font-size: 12px;
  }
}
input[type=checkbox]:disabled + label,
input[type=radio   ]:disabled + label {
  color: $text-color-light;
  cursor: default;
}
input[type=checkbox]:checked:disabled + label > span:first-child {
  background-color: $inputs-border-color!important;
  border-color: $inputs-border-color!important;
}
input[type=checkbox] + label > span:first-child{
  position: absolute;
  left: 0;
  top: 0;
  display: inline-block;
  flex-shrink: 0;
  width: 22px;
  height: 22px;
  border: 1px solid $inputs-border-color;
  transition: background-color .25s;
  &:before{
    content     : '';
    display     : inline-block;
    @include size(10px,7px);
    background-image: url('../images/tick-white.svg');
    background-repeat: no-repeat;
    background-size: 10px 8px;
    transform: scale(0);
    opacity: 0;
    transition: transform .25s, opacity .25s;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }

}
input[type=radio] + label > span:first-child{
  position: absolute;
  left: 0;
  top: 0;
  display: inline-block;
  flex-shrink: 0;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  border: 1px solid $inputs-border-color;
  background-color: #fff;
  transition: background-color .25s;
  &:before{
    content     : '';
    display     : inline-block;
    @include size(10px,10px);
    background-color: $theme-color;
    border-radius: 50%;
    transform: scale(0);
    opacity: 0;
    transition: transform .25s, opacity .25s;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }
}
input[type=radio]:disabled:checked + label > span:first-child {
  &:before {
    background-color: $inputs-border-color!important;
  }
}
input[type=radio].accent-radio + label > span:first-child {
  &:before {
    background-color: $theme-color-4-light;
  }
}
input[type=radio] + label > span:last-child{
  margin-top: .2em;
  @media (max-width: 480px) {
    margin-top: .3em;
  }
}
input[type=checkbox].sm-checkbox + label {
  min-height: 16px;
  font-size: 14px;
  padding-left: 24px;
}
input[type=checkbox].sm-checkbox + label > span:first-child {
  width: 16px;
  height: 16px;
}
input[type=checkbox].sm-checkbox + label > span:last-child{
  display: inline-block;
  line-height: 16px;
  min-height: 16px;
}
input[type=checkbox].sm-checkbox + label > span:first-child:before {
  //width: 7px;
  //height: 5px;
}
input[type=checkbox].dark-marker-checkbox + label > span:first-child{
  border: 0;
  background-color: #fff;
  &:before {

  }
}
input[type=checkbox] + label > span:last-child {
  line-height: 1.3;
  min-height: 22px;
  display: inline-flex;
  align-items: center;
}
input[type=checkbox]:checked + label > span:first-child{
  border-color: $theme-color;
  background-color: $theme-color;
}
input[type=radio]:checked + label > span:first-child{
  border-color: $theme-color;
}
input[type=checkbox].accent-checkbox:checked + label > span:first-child{
  border-color: $theme-color-4-light;
  background-color: $theme-color-4-light;
}

input[type=radio].accent-radio:checked + label > span:first-child{
  border-color: $inputs-border-color;
}
input[type=checkbox].dark-marker-checkbox:checked + label > span:first-child{
  background-color: $theme-color-2;
}
input[type=checkbox] + label span{
  outline: none;
}
input[type=checkbox]:checked + label > span:first-child:before{
  transform: scale(1);
  opacity:1;
}
input[type=radio]:checked + label > span:first-child:before{
  transform: scale(1);
  opacity:1;
}
.text-radio-row {
  display: flex;
  width: 100%;
  margin-bottom: 30px;
  .text-radio-wrap {
    flex: 1 1 100%;
    margin: 0;
    &:first-child {
      label {
        border-radius: 24px 0 0 24px;
      }
    }
    &:nth-child(2n) {
      label {
        border-left: 0;
      }
    }
    &:last-child {
      label {
        border-radius: 0 24px 24px 0;
      }
    }
    label {
      width: 100%;
      height: 48px;
      border: 1px solid #ffffff;
      color: #ffffff;
      padding: 0 10px;
      font-size: 18px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      transition: none;
      @media (max-width: 600px) {
        font-size: 16px;
      }
    }
    input[type=radio]:checked + label {
      color: $text-color-base;
      background-color: #fff;
    }
  }
}
input[type=checkbox].text-checkbox + label {
  min-width: 160px;
  height: 36px;
  border-radius: 18px;
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  border: 1px solid $theme-color;
  background-image: url("../images/tick-grey.svg");
  background-repeat: no-repeat;
  background-size: 10px 8px;
  background-position: calc(100% - 20px) 14px;
  transition: none;
  padding: 0 20px;
  font-size: 14px;
  .svg-icon {
    fill: $theme-color;
    transition: none;
  }
  & > span.check-icon-wrap {
    width: 20px;
    margin-right: 8px;
    display: inline-flex;
    align-items: center;
    justify-content: flex-end;
    border: 0;
    position: static;
    transition: none;
  }
}
input[type=checkbox].text-checkbox:checked + label {
  background-color: $theme-color;
  border-color: $theme-color;
  background-image: url("../images/tick-white.svg");
  color: #ffffff;
  .svg-icon {
    fill: #ffffff;
  }
  & > span.check-icon-wrap {
    &:before {
      display: none;
    }
  }

}

.files-input {
  position: relative;
  &__input {
    opacity: 0;
    visibility: hidden;
    position: absolute;
  }
  &__label {
    @extend .btn;
    width: 30px;
    height: 30px;
    position: relative;
    overflow: visible;
    .svg-icon {
      fill: $text-color-light;
      //._filled &{
      //  fill: $theme-color;
      //}
    }
    &:hover {
      .svg-icon {
        fill: $theme-color;
      }
    }
  }
  &__count {
    font-size: 10px;
    line-height: 1;
    position: absolute;
    right: 0;
    top: -3px;
    width: 15px;
    height: 15px;
    background-color: $theme-color;
    color: #ffffff;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    display: none;
    ._filled & {
      display: inline-flex;
    }
  }
}


input[type=checkbox].switch-checkbox + label {
  display: inline-flex;
  align-items: center;
  font-size: 14px;
  min-height: 22px;
  padding-left: 0;
}
input[type=checkbox].switch-checkbox + label > span{
  &:first-child {
    display: inline-block;
    flex-shrink: 0;
    @include size(40px,22px);
    background-color: $theme-color-2;
    border: 0;
    margin           : 0 11px 0 0;
    border-radius    : 11px;
    vertical-align   : bottom;
    transition: all .25s;
    position: relative;
    &:before {
      content: '';
      display: inline-block;
      width: 14px;
      height: 14px;
      background-color: #fff;
      background-image: none;
      border-radius: 50%;
      position: absolute;
      left: 4px;
      top: 4px;
      margin: 0;
      transition: all .25s;
      transform: none;
      opacity: 1;
    }
  }
  &:last-child {
    margin-top: 1px;
  }
}

input[type=checkbox].switch-checkbox:checked + label > span{
  &:first-child {
    background-color: $theme-color;
    &:before {
      content: '';
      display: inline-block;
      width: 14px;
      height: 14px;
      background-color: #fff;
      border-radius: 50%;
      position: absolute;
      left: 4px;
      top: 4px;
      transform: translateX(17px);
    }
  }
}
input[type=checkbox].switch-checkbox--accent:checked + label > span {
  &:first-child {
    background-color: $theme-color-4-light;
  }
}

input[type=checkbox] + label,
input[type=radio   ] + label {
  &.tutor-check-label {
    border-radius: 10px;
    background-color: $bg-color;
    padding: 30px;
    width: 100%;
    border: 1px solid transparent;
    @media (max-width: 1000px) {
      padding: 20px;
    }
    & > span {
      bottom: -10px;
      left: 0;
      right: 0;
      top: auto;
      margin: auto;
    }
  }
}

input[type=checkbox]:checked + label,
input[type=radio   ]:checked + label {
  &.tutor-check-label {
    background-color: #fff;
    border-color: $theme-color-4-light;
  }
}


.check-text-wrap{
  input[type=checkbox] + label,
  input[type=radio] + label{
    height: 50px;
    background-color: #ffffff;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    line-height: 1;
    padding: 10px 38px;
    border-radius: 25px;
    font-size: 18px;
    transition: background-color .25s;
    @media (max-width: 480px) {
      height: 40px;
      padding: 0 23px;
      border-radius: 20px;
      font-size: 16px;
    }
    &:hover{
      background-color: $theme-color;
      color: #ffffff;
      .svg-icon{
        fill: #ffffff;
      }
    }
    .svg-icon, img{
      margin-right: 10px;
      transition: none;
      @media (max-width: 600px) {
        margin-right: 10px;
      }
    }
    & > span:first-child {
      position: relative;
      z-index: 2;
      width: auto;
      height: auto;
      border: 0;
      background-color: transparent;
      margin: 0;
      padding: 0;
      pointer-events: none;
      &:before {
        display: none;
      }
    }
    &.btn-label--accent {
      background-color: $theme-color-4-light;
      color: #ffffff;
      .svg-icon {
        fill: #ffffff;
      }
    }
  }

  input[type=checkbox]._active + label,
  input[type=radio]._active + label {
    background-color: $theme-color;
    color: #ffffff;
  }

  input[type=checkbox]:checked + label,
  input[type=radio]:checked + label{
    background-color: $theme-color;
    color: #ffffff;
    .svg-icon{
      fill: #ffffff;
    }
    &.btn-label--accent {
      background-color: $theme-color-4-light;
      color: #ffffff;
      .svg-icon {
        fill: #ffffff;
      }
    }
  }

  input[type=checkbox][disabled] + label,
  input[type=radio][disabled] + label {
    background-color: #eeeeee;
    color: $text-color-light;
  }

  &--symbol{
    input[type=checkbox] + label,
    input[type=radio] + label{
      width: 85px;
      padding: 0;
      @media (max-width: 480px) {
        width: 60px;
      }
    }
  }
  &--sm{
    input[type=checkbox] + label,
    input[type=radio] + label{
      height: 40px;
      padding: 0 23px;
      border-radius: 20px;
      font-size: 16px;
    }
    .checkbox-group & {
      margin: 10px 5px 0;
      @media (max-width: 600px) {
        margin: 8px 4px 0;
      }
    }
  }
  //&--xxs{
  //  input[type=checkbox] + label,
  //  input[type=radio] + label{
  //    height: 40px;
  //    padding: 0 15px;
  //    border-radius: 27.5px;
  //    font-size: 18px;
  //  }
  //  .checkbox-group & {
  //    margin: 10px 5px 0;
  //    @media (max-width: 600px) {
  //      margin: 8px 4px 0;
  //    }
  //  }
  //}
}

.checkbox-group {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 100%;
  margin: -20px -10px 0;
  @media (max-width: 480px) {
    margin: -10px -5px 0;
  }
  .check-text-wrap {
    margin: 20px 10px 0;
    @media (max-width: 480px) {
      margin: 10px 5px 0;
    }
    label {
      width: 100%;
    }
  }
  &--double {
    .check-text-wrap {
      width: 210px;
      @media (max-width: 480px) {
        width: 100%;
      }
    }
  }
}

label.btn-label {
  &--accent {
    background-color: $theme-color-4-light;
  }
}

