.param{
  display: inline-flex;
  align-items: center;
  &__label {
    color: $text-color-light;
    margin-right: 3px;
  }
  &__value {

  }
}

.params-tbl {
  &--sm {
    font-size: 14px;
  }
  &--grid {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin: -12px 0 0;
  }
  &--columns {
    column-count: 2;
    column-gap: 50px;
  }
  &__row {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    //border-bottom: 1px solid $border-color;
    margin-bottom: 10px;
    margin-top: -5px;
    padding-top: 5px;
    position: relative;
    &:after {
      content: '';
      display: block;
      width: 100%;
      height: 1px;
      background-color: $border-color;
      position: absolute;
      left: 0;
      right: 0;
      top: 1em;
    }
    .params-tbl--sm & {
      margin-bottom: 12px;
    }
    .params-tbl--grid & {
      width: calc(50% - 25px);
      &:nth-child(2n + 1) {
        margin-right: 25px;
      }
      &:nth-child(2n) {
        margin-left: 25px;
      }
    }
    .params-tbl--columns & {
      break-inside: avoid;
    }
    &:last-child {
      margin-bottom: 0;
    }
    span {
      display: inline-block;
      background-color: #fff;
      margin-bottom: -1px;
      position: relative;
      z-index: 1;
      &:first-child {
        padding-right: 15px;
        color: $text-color-light;
      }
      &:last-child {
        padding-left: 15px;
        text-align: right;
      }
      img {
        margin-right: 8px;
      }
    }
    span.inline-tutor {
      padding-left: 55px;
      position: relative;
      .inline-ava {
        position: absolute;
        left: 15px;
        top: -4px;
        margin: 0;
      }
    }
  }
}
