.screen {
  background: #FFFFFF;
  border-radius: 12px 12px 10px 10px;
  overflow: hidden;
  color: $text-color-base;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  &__header {
    width: 100%;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 16px;
    position: relative;
    &:after {
      content: '';
      display: inline-block;
      width: 20px;
      height: 4px;
      background-image: url("../images/dots.svg");
      background-repeat: no-repeat;
      position: absolute;
      top: 12px;
      right: 16px;
    }
    &--active {
      background-color: $theme-color-3;
      &:after {
        filter: grayscale(1);
      }
    }
    .screen--sm & {
      height: 30px;
      border-bottom: 1px solid $bg-color;
    }
  }
  &__header-title {
    font-size: 14px;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__num {
    line-height: 1;
    font-size: 14px;
    font-weight: 500;
  }
  &__img {
    margin: 0 0 3px 3px;
  }
  &__body {
    padding: 50px 40px;
    display: flex;
    align-items: center;
    //justify-content: center;
    justify-content: flex-start;
    flex-direction: column;
    position: relative;
    flex: 1 1 auto;
    width: 100%;
    .screen--left & {
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      padding: 50px;

    }
    &--no-pad {
      padding: 0;
    }
    &--sm-top-pad {
      padding-top: 20px;
      .screen--left & {
        padding-top: 20px;
      }
    }
    &--mob--sm-pad {
      @media (max-width: 640px) {
        padding: 20px;
        .screen--left & {
          padding: 20px;
        }
      }
    }
  }
  &__title {
    display: flex;
    align-items: flex-start;
    font-weight: 500;
    font-size: 24px;
    margin-bottom: 30px;
    @media (max-width: 480px) {
      font-size: 16px;
      margin-bottom: 18px;
    }
    & > .svg-icon {
      margin-right: 18px;
      margin-top: -.35em;
      @media (max-width: 480px) {
        margin-right: 14px;
        width: 30px;
        margin-top: -.5em;
        &.svg-icon-webcam{
          width: 26px;
        }
      }
    }
  }
}
