.step-progress {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 -2px;
  span{
    display: inline-block;
    width: 100px;
    height: 8px;
    border-radius: 8px;
    margin: 0 2px;
    background-color: $text-color-light;
    overflow: hidden;
    position: relative;
    &:before {
      content: '';
      display: inline-block;
      width: 0;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background-color: $theme-color-4-light;
    }
    &._current {
      &:before {
        width: 50%;
      }
    }
    &._passed {
      &:before {
        width: 100%;
      }
    }
  }
}

.step{
  background-color: $bg-color;
  position: relative;
  padding-bottom: 105px;
  padding-top: 100px;
  @media (max-width: 480px) {
    padding-top: 40px;
  }
  &--no-top-pad {
    padding-top: 0;
  }
  @media (max-width: 480px) {
    padding-bottom: 40px;
  }
  &__inner{
    width: 1100px;
    max-width: 100%;
    height: 100%;
    margin: 0 auto;
    padding: 0 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    & > form {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
    }
  }
  &__title{
    font-size: 36px;
    line-height: 1.25;
    font-weight: 500;
    text-align: center;
    margin-bottom: 40px;
    @media (max-width: 480px) {
      font-size: 24px;
    }
    div {
      &:first-child {
        font-size: 20px;
        font-weight: 500;
        color: $text-color-light;
      }
    }
  }
  &__progress{
    margin-top: -4px;
    margin-bottom: 76px;
    @media (max-width: 480px) {
      margin-bottom: 30px;
    }
  }
  &__body {
    margin-top: 50px;
    @media (max-width: 480px) {
      margin-top: 20px;
    }
  }
  &__choice{
    width: 100%;
  }
  &__choice--begining{
    form{

    }
  }
  &__choice--time{
    .time-setting{
      &__body{
        //padding-top: 36px;
        padding-bottom: 20px;
        .checkbox-group {
          margin: -10px -5px 0;
          @media (max-width: 440px) {
            margin: 0;
          }

        }
        .check-text-wrap {
          width: 90px;
          margin: 10px 5px 0;
          @media (max-width: 480px) {
            width: 80px;
          }
        }
      }
    }
    & > form {

      & > .checkbox-group {
        margin-left: 0;
        margin-right: 0;
        &:first-child {
          margin-bottom: 20px;
          @media (max-width: 480px) {
            margin-bottom: 10px;
          }
        }
        & > .day-time-holder {
          & > .check-text-wrap {
            width: 180px;
            @media (max-width: 440px) {
              width: 160px;
            }
            @media (max-width: 380px) {
              width: 140px;
            }
            @media (max-width: 340px) {
              width: 128px;
            }
          }
        }
      }
    }
  }
  &__nav{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin-top: 30px;
    @media (max-width: 480px) {
      margin-top: 0;
    }
  }
  &__nav-btn{
    width: 180px;
    margin-top: 20px;
    @media (max-width: 480px) {
      width: 100%;

    }
    &--hidden{
      display: none;
      ._opened & {
        display: inline-flex;
      }
    }
  }
}

.time-note {
  text-align: center;
  margin-top: 40px;
  color: $text-color-light;
  font-size: 16px;
}




