.video-play {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 12px 0 12px 16px;
  border-color: transparent transparent transparent #ffffff;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  transition: border-color .25s;
}

.video-wrapper {
  position: relative;
  cursor: pointer;
  &:hover {
    .video-play {
      border-color: transparent transparent transparent $theme-color;
    }
  }
}
