.steps {
  display: flex;
  justify-content: center;
  flex-wrap: nowrap;
  margin: 0 -10px;
  //@media (max-width: 1200px) {
  //  margin: 0;
  //  justify-content: flex-start;
  //  overflow: auto;
  //  margin-right: -40px;
  //  padding-bottom: 1px;
  //}
  //@media (max-width: 600px) {
  //  margin-right: -20px;
  //}
  @media (max-width: 768px) {
    flex-wrap: wrap;
    justify-content: center;
    margin-top: -30px;
  }
  &__item {
    width: 400px;
    margin: 0 10px;
    flex: 0 0 400px;
    //@media (min-width: 1201px) and (max-width: 1400px) {
    //  width: calc(33.3333% - 20px);
    //  flex: 1 1 auto;
    //}
    @media (max-width: 1400px) {
      width: calc(33.3333% - 20px);
      flex: 1 1 auto;
    }
    @media (max-width: 768px) {
      width: calc(100% - 20px);
      flex: 0 1 400px;
      margin-top: 30px;
      display: flex;
      flex-direction: column;
    }
  }
  &__tick {
    width: 30px;
    height: 23px;
    display: block;
    background-image: url("../images/tick.svg");
    background-repeat: no-repeat;
    background-position: center;
    margin: -15px auto 0;
    position: relative;
    z-index: 2;
    @media (max-width: 768px) {
      height: 28px;
    }
  }
  &__title {
    font-size: 18px;
    font-weight: 500;
    margin-top: 23px;
    text-align: center;
  }
}
