.mob-scrolling {
  overflow: hidden;
  @media (max-width: 768px) {
    display: block!important;
    margin: 0 -40px!important;
  }
  @media (max-width: 600px) {
    margin: 0 -20px!important;
  }
  &:after {
    content: '';
    @media (max-width: 768px) {
      content: 'flickity';
      display: none;
    }
  }
  &--mob-stack {
    &:after {
      @media (max-width: 480px) {
        content: '';
        display: inline-block;
      }
    }
  }
  .flickity-slider{
    @media (max-width: 768px) {
      margin-left: 40px;
      margin-right: 40px;
    }
    @media (max-width: 600px) {
      margin-left: 20px;
      margin-right: 20px;
    }
    & > * {
      &:last-child {
        @media (max-width: 768px) {
          padding-right: 40px;
        }
        @media (max-width: 600px) {
          padding-right: 20px;
        }
      }
      @media (max-width: 768px) {
        //height: 100%;
        //min-height: 100%;
      }
      & > * {
        @media (max-width: 768px) {
          //height: auto;
          //width: 100%;
          //position: absolute;
          //width: 100%;
          height: 100%;
        }
      }
    }
  }
  .flickity-viewport {
    @media (max-width: 768px) {
      //width: calc(100% + 40px);
      padding-right: 40px;
    }
  }
}
