.support {
  display: flex;
  @media (max-width: 1000px) {
    flex-wrap: wrap;
  }
  &__main {
    flex: 1 1 auto;
    padding-right: 140px;
    @media (max-width: 1400px) {
      padding-right: 70px;
    }
    @media (max-width: 1000px) {
      padding-right: 0;
    }
  }
  &__sidebar {
    flex: 0 0 400px;
    @media (max-width: 1000px) {
      width: 100%;
      flex: 1 1 auto;
      margin-top: 40px;
    }
    .support-card {
      @media (max-width: 1000px) {
        height: 204px;
      }
      &__img {
        @media (max-width: 1000px) {
          width: 194px;
          bottom: -82px;
        }
        @media (max-width: 480px) {
          width: auto;
          bottom: 0;
        }
      }
    }
  }
}

.support-card {
  border-radius: 12px 12px 10px 10px;
  background-color: $bg-color;
  padding: 40px 40px 295px;
  position: relative;
  font-size: 16px;
  overflow: hidden;
  @media (max-width: 1000px) {
    padding: 40px;
  }
  @media (max-width: 480px) {
    padding: 20px;
  }
  &__img {
    display: block;
    position: absolute;
    right: 0;
    bottom: 0;
  }
  &__title {
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 30px;
    @media (max-width: 1000px) {
      margin-bottom: 25px;
    }
    @media (max-width: 480px) {
      font-size: 18px;
      margin-bottom: 17px;
    }
  }
  &__list {
    width: 55%;
    li {
      margin-bottom: 32px;
      @media (max-width: 1000px) {
        margin-bottom: 25px;
      }
      &:last-child {
        margin-bottom: 0;
      }
      .icon-wrap {
        width: 26px;
        margin-right: 12px;
      }
      .svg-icon {

      }
      a{
        display: flex;
        color: $text-color-base;
        &:hover {
          color: $link-color-hover;
        }
      }
    }
  }
}
