.chat{
  width: 1030px;
  max-width: 100%;
  display: flex;
  background-color: #fff;
  @media (max-width: 540px) {
    flex-wrap: wrap;
  }
  &__sidebar {
    width: 295px;
    flex: 0 0 auto;
    background-color: $bg-color;
    @media (max-width: 700px) {
      width: 240px;
    }
    @media (max-width: 540px) {
      width: 100%;
      position: relative;
    }
  }
  &__search-form {
    padding: 15px 20px;
    border-bottom: 1px solid $border-color;
    @media (max-width: 540px) {
      padding-right: 60px;
    }
  }
  &__search-input{
    height: 40px;
    border-radius: 20px;
  }
  &__users {
    @media (max-width: 540px) {
      width: 100%;
      //height: 71px;
      position: absolute;
      top: 71px;
      //box-shadow: $box-shadow;
      display: flex;
      flex-direction: column;
      align-items: stretch;
      justify-content: flex-start;
    }
   &._opened {
     @media (max-width: 540px) {
       box-shadow: $box-shadow;
     }
   }
  }
  &__user {
    display: flex;
    align-items: center;
    padding: 15px 20px;
    border-bottom: 1px solid $border-color;
    cursor: pointer;
    background-color: $bg-color;
    @media (max-width: 540px) {
      order: 1;
    }

    &._current {
      background-color: $theme-color;
      border-bottom-color: transparent;
      @media (min-width: 541px) {
        cursor: default;
      }
      @media (max-width: 540px) {
        order: 0;
      }
      &:after{
        content: '';
        display: inline-block;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 4px 5px 0 5px;
        border-color: #ffffff transparent transparent transparent;
        transition: transform .25s;
        position: absolute;
        top: 33px;
        right: 20px;
        @media (min-width: 541px) {
          display: none;
        }
      }
      .chat__users._opened & {
        &:after {
          transform: scaleY(-1);
        }
      }
    }
    &:not(._current) {
      @media (max-width: 540px) {
        display: none;
      }
      .chat__users._opened & {
        @media (max-width: 540px) {
          display: flex;
        }
      }
    }
  }
  &__user-ava {
    margin-right: 10px;
    width: 40px;
    height: 40px;
    background-color: $theme-color;
    background-image: url("../images/ava.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 22px 22px;
    border-radius: 50%;
    img{
      display: block;
      border-radius: 50%;
    }
  }
  &__user-info {

  }
  &__user-label {
    font-size: 12px;
    line-height: 1;
    color: $text-color-light;
    ._current & {
      color: #9ACAEB;
    }
  }
  &__user-name {
    font-size: 16px;
    font-weight: 500;
    ._current & {
      color: #ffffff;
    }
  }
  &__user-last-time {
    margin-left: auto;
    font-size: 12px;
    line-height: 1;
    color: $text-color-light;
  }
  &__main {
    flex: 1 1 auto;
    padding: 40px;
    @media (max-width: 700px) {
      padding: 40px 20px 27px;
    }
    @media (max-width: 540px) {
      padding: 20px 20px 27px;
      margin-top: 71px;
    }
  }
  &__messages-stack {
    //display: flex;
    max-height: 460px;
    min-height: 460px;
    overflow: auto;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    padding-bottom: 40px;
    display: none;
    &._opened {
      display: flex;
    }
  }
  &__messages-title {
    font-size: 16px;
    font-weight: 500;
    line-height: 1;
    margin-bottom: 14px;
    margin-top: 20px;
    @media (max-width: 540px) {
      font-size: 14px;
      margin-bottom: 10px;
    }
    &--own {
      margin-left: auto;
    }
    &:first-child {
      margin-top: 0;
    }
    & + .chat__message {
      margin-top: 0;
    }
  }
  &__message {
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    border-radius: 10px;
    padding: 10px 20px;
    background-color: $bg-color;
    margin-top: 6px;
    &--own {
      background-color: $theme-color-light;
      margin-left: auto;
    }
  }
  &__message-text {
    font-size: 14px;
  }
  &__message-meta {
    color: $text-color-light;
    font-size: 12px;
    line-height: 1;
    margin-top: 3px;
  }
  &__message-form {
    position: relative;
  }
  &__message-files {
    width: 30px;
    height: 30px;
    position: absolute;
    top: 5px;
    right: 80px;

  }
  &__message-submit-btn {
    height: 30px;
    border-radius: 15px;
    width: 70px;
    min-width: 70px;
    padding: 0 10px;
    font-size: 14px;
    position: absolute;
    right: 5px;
    top: 5px;
  }
}

input.chat__message-input {
  height: 40px;
  border-radius: 20px;
  padding-right: 110px;
}
input.chat__search-input {
  height: 40px;
  border-radius: 20px;
  background-color: #ffffff;
}
