.big-datepicker-holder{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  .datepicker {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
  .datepicker-inline {
    max-width: 100%;
  }
  .datepicker--content {
    width: 736px;
    max-width: 100%;
  }
}


.inline-datepicker-input{
  display: none;
}

.datepicker{
  background-color: transparent;
  &.big-datepicker{
    width: 904px;
    max-width: 100%;
    border: 0;
    .datepicker--cell{
      width: 85px;
      height: 50px;
      background-color: #ffffff;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      font-size: 18px;
      font-weight: 400;
      border-radius: 25px;
      font-family: $ff-base;
      margin: 20px 10px 0;
      transition: background-color .25s;
      @media (max-width: 768px) {
        flex: 1 1 auto;
      }
      @media (max-width: 480px) {
        width: 32px;
        height: 32px;
        font-size: 14px;
        border-radius: 50%;
        margin: 10px 5px 0;
        flex: 0 0 32px;
      }
      &.datepicker--cell-day{
        &.-selected-,
        &.-focus-{
          background-color: $theme-color;

          color: #ffffff;
        }
        &.-current-{
          color: $theme-color-4-light;
          &:hover{
            color: #ffffff;
          }
          &.-selected-{
            color: #ffffff;
          }
        }
        &.-in-range-{
          background-color: #d0ebfd;
          //color: #ffffff;
        }
      }
    }
    .datepicker--days-names {
      display: none;
    }
    //.datepicker--day-name{
    //  width: 93px;
    //  font-size: 24px;
    //  font-family: $ff-base;
    //  font-weight: 400;
    //  color: #ababab;
    //  text-transform: none;
    //  @media (max-width: 1600px){
    //    width: 56px;
    //    font-size: 15px;
    //  }
    //  @media (max-width: 600px) {
    //    width: 23px;
    //    font-size: 12px;
    //  }
    //}
    .datepicker--cell-day.-other-month-, .datepicker--cell-year.-other-decade-{
      background-color: #eeeeee;
      //border: 1px solid #EDEFF1;
      color: $text-color-light;
    }
    .datepicker--cells {
      &.datepicker--cells-days{
        //margin: 0 - 17.5px 0;
      }
    }
    .datepicker--nav{
      border-bottom: 0;
      justify-content: center;
      margin-bottom: 0;
      @media (max-width: 1600px){
        margin-bottom: 10px;
      }
      @media (max-width: 600px) {
        margin-bottom: 0;
        display: flex;
        align-items: center;
      }
    }
    .datepicker--nav-title{
      font-size: 18px;
      font-weight: 500;
      pointer-events: none;
      padding: 0 20px;
      i {
        color: $text-color-base
      }
    }
    .datepicker--nav-action{
      border-radius: 50%;
      width: 30px;
      height: 30px;
      position: relative;
      transition: background-color .25s;
      &[data-action="prev"]{
        &:before {
          border-width: 5.5px 4px 5.5px 0;
          border-color: transparent $text-color-light transparent transparent;
          left: -1px;
        }
        &:hover {
          &:before {
            border-color: transparent #ffffff transparent transparent;
          }
        }
      }
      &[data-action="next"]{
        &:before {
          border-width: 5.5px 0 5.5px 4px;
          border-color: transparent transparent transparent $text-color-light;
          right: -1px;
        }
        &:hover {
          &:before {
            border-color: transparent transparent transparent #ffffff;
          }
        }
      }
      &:hover{
        background-color: $theme-color;

      }

      &:before {
        content: '';
        display: inline-block;
        width: 0;
        height: 0;
        border-style: solid;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
      }
    }
  }
}

.date-time-picker {
  z-index: 9999;
  border-radius: 0;
  box-shadow: $box-shadow;
  border-color: $border-color;
  .datepicker--pointer {
    border-color: $border-color;
  }
  .datepicker--nav-action {
    .svg-icon {
      fill: $text-color-light;
      width: 6px;
      height: 10px;
    }
  }
  .datepicker--cell-day {
    &.-selected- {
      background-color: $theme-color-2;
      color: #ffffff;
    }
    &.-current- {
      color: $theme-color-2;
    }
  }
  .datepicker--day-name {
    color: $theme-color;
  }
}
