.steps-list {
  display: flex;
  align-items: center;
  margin: -16px -15px 0;
  @media (max-width: 700px) {
    flex-wrap: wrap;
  }
  li {
    margin: 16px 15px 0;
    font-size: 14px;
    font-weight: 500;
    display: flex;
    align-items: center;
    @media (max-width: 700px) {
      width: 100%;
    }
    &._completed {
      &:before {
        background-color: $theme-color-4-light;
      }
    }
    &:before {
      content: '';
      display: inline-block;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      background-color: $border-color;
      margin-right: 10px;
      background-image: url("../images/tick-white.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 10px 8px;
      flex-shrink: 0;
    }
  }
}

.becoming-steps {
  display: flex;
  align-items: center;
  justify-content: space-between;
  @media (max-width: 1024px) {
    flex-wrap: wrap;
  }
  &__note {
    width: 200px;
    font-size: 14px;
    line-height: 17px;
    color: $text-color-light;
    @media (max-width: 1024px) {
      width: 100%;
    }
  }
  &__steps {
    @media (max-width: 1024px) {
      width: 100%;
      margin-top: 20px;
    }
    @media (max-width: 700px) {
      margin-top: 0;
    }
  }
  &__submit {
    @media (max-width: 1024px) {
      margin-top: 22px;
    }
  }
}


