.overlay{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  display: none;
  background-color: #1b1b1b;
  opacity: .5;
  &--sidebar {
    z-index: 6;
  }
  &._visible{
    display: block;
    animation: overlay-fade-in;
    animation-duration: .25s;
    animation-fill-mode: forwards;
  }
  &._hidden{
    animation: overlay-fade-out;
    animation-duration: .25s;
    animation-fill-mode: forwards;
  }
}

.light-overlay {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color:#ffffff;
  opacity: .4;
  top: 0;
  left: 0;
  z-index: 5;
  cursor: default;
  &._visible{
    display: block;
    animation: overlay-fade-in;
    animation-duration: .25s;
    animation-fill-mode: forwards;
  }
  &._hidden{
    animation: overlay-fade-out;
    animation-duration: .25s;
    animation-fill-mode: forwards;
  }
}
