.tooltip {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  bottom: calc(100% + 6px);
  background-color: $theme-color-2;
  color: #ffffff;
  text-align: center;
  align-items: center;
  justify-content: center;
  padding: 12px 25px 11px;
  box-shadow: 0 12px 14px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  display: none;
  z-index: 1;
  &:after {
    content: '';
    display: inline-block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 6px 6px 0 6px;
    border-color: $theme-color-2 transparent transparent transparent;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    bottom: -6px;
  }
  &--light {
    background-color: #fff;
    border: 1px solid $border-color;
    width: 200px;
    border-radius: 20px;
    bottom: auto;
    margin-left: -91px;
    top: calc(100% + 15px);
    color: $text-color-light;
    font-size: 14px;
    font-weight: 400;
    text-align: left;
    line-height: 1.3;
    padding: 20px;
    box-shadow: $box-shadow-light;
    &:after {
      width: 10px;
      height: 10px;
      background-color: #fff;
      border-left: 0;
      border-bottom: 0;
      border-top: 1px solid $border-color;
      border-right: 1px solid $border-color;
      bottom: auto;
      top: -6px;
      left: 0;
      right: 0;
      margin: auto;
      transform: rotate(-45deg);
    }
  }
  &._visible{
    display: flex;
    animation: up-fade-in;
    animation-duration: .25s;
    animation-fill-mode: forwards;
  }
  &._hidden{
    animation: down-fade-out;
    animation-duration: .25s;
    animation-fill-mode: forwards;
  }
  &._hidden-up{
    animation: up-fade-out;
    animation-duration: .25s;
    animation-fill-mode: forwards;
  }
  &--inline {
    left: 0;
    right: auto;
    margin: 0;
    &:after {
      left: 30px;
      right: auto;
    }
  }
}

.has-tooltip {
  position: relative;
}

.tooltip-label {
  width: 20px;
  height: 20px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: 1px solid $text-color-light;
  cursor: pointer;
  transition: border-color .25s;
  position: relative;
  margin-left: 12px;
  &:hover {
    border-color: $theme-color;
    .svg-icon {
      fill: $theme-color;
    }
  }
  .svg-icon {
    fill: $text-color-light;
  }
}

.tooltip-element {
  position: relative;
}
