.calendar {
  &._opened {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 40px;
  }
  &__open-btn {
    @extend .btn;
    font-size: 18px;
    font-weight: 500;
    border-bottom: 1px solid $text-color-ultralight;
    &:hover {
      border-bottom-color: transparent;
    }
  }
  &__content {
    display: none;
    ._opened & {
      display: block;
    }
  }
  &__total {
    margin-top: 40px;
    text-align: center;
    font-size: 16px;
    span {
      color: $text-color-light;
    }
  }
  .catalog-controls & {
    margin-right: 10px;
  }
  .datepicker--nav-title {
    font-size: 18px;
    font-weight: 500;
    font-family: $ff-base;
  }
  .datepicker.big-datepicker {
    width: 536px;
    padding-top: 20px;
  }
  .datepicker.big-datepicker .datepicker--cell{
    height: 40px;
    width: 55px;
    border-radius: 20px;
    font-size: 16px;
  }
}

input.datepicker-input {
  display: none;
}
