.appeal {
  background-color: $bg-color;
  padding-top: 22px;
  @media (max-width: 480px) {
    padding-top: 40px;
  }
  &__inner {
    @extend .container;
    display: flex;
    justify-content: space-between;
    @media (max-width: 700px) {
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
    }
  }
  &__caption {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    width: 478px;
    margin-top: -22px;
    @media (max-width: 700px) {
      width: 100%;
      margin-bottom: 30px;
      margin-top: 0;
    }
    @media (max-width: 480px) {

    }
  }
  &__media {
    @media (min-width: 701px) and (max-width: 1000px) {
      margin-right: -130px;
    }
  }
  &__title {
    font-size: 44px;
    margin-bottom: 30px;
    @media (max-width: 768px) {
      margin-bottom: 20px;
    }
    @media (max-width: 700px) {
      font-size: 36px;
    }
    @media (max-width: 480px) {
      font-size: 30px;
      text-align: center;
      margin-bottom: 20px;
    }
  }
  &__intro {
    font-size: 18px;
    line-height: 1.5;
    @media (max-width: 480px) {
      font-size: 16px;
      text-align: center;
    }
  }
  &__action-btn {
    margin-top: 50px;
    @media (max-width: 768px) {
      margin-top: 30px;
    }
  }
  &__media {

  }
}
