.input-wrap {
  position: relative;
  margin-bottom: 30px;
  line-height: 0;
  @media (max-width: 600px) {
    margin-bottom: 20px;
  }
  label {
    font-size: 18px;
    font-weight: 500;
    line-height: 1.2;
    display: block;
    margin-bottom: 16px;
    @media (max-width: 480px) {
      font-size: 14px;
      margin-bottom: 10px;
    }
  }
}

.input-row {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  margin: -20px -10px 20px;
  &:last-child {
    margin-bottom: 0;
  }
  .input-wrap {
    margin: 20px 10px 0;
    flex: 1 1 calc(50% - 20px);
    @media (max-width: 480px) {
      flex: 1 1 auto;
    }
  }
  &--lg-margin {
    margin-bottom: 30px;
  }
  &--3col {
    .input-wrap {
      flex: 1 1 calc(33.3333% - 20px);
    }
  }
  & > button {
    margin: 20px 10px 0;
    flex: 0 1 auto;
  }
}

.icon-label {
  position: absolute;
  left: 30px;
  top: 0;
  bottom: 0;
  margin: 0!important;
  display: inline-flex!important;
  align-items: center;
  @media (max-width: 600px) {
    left: 22px;
  }
}

input.icon-input {
  padding-left: 60px;
  @media (max-width: 600px) {
    padding-left: 48px;
  }
}

input {
  &._filled {
    background-image: url("../images/tick-white.svg");
    background-size: 14px 11px;
    background-repeat: no-repeat;
    background-position: calc(100% - 30px) 25px;
    padding-right: 55px;
    @media (max-width: 600px) {
      background-position: calc(100% - 20px) 16px;
      padding-right: 40px;
    }
    & + label {
      border-color: $inputs-focus-border-color;
      .svg-icon {
        //fill: #ffffff;
      }
    }
  }
}

label {
  .svg-icon-mail {
    @media (max-width: 600px) {
      width: 16px;
    }
  }
  .svg-icon-key {
    @media (max-width: 600px) {
      width: 16px;
    }
  }
  .svg-icon-user {
    @media (max-width: 600px) {
      width: 16px;
      height: 18px;
    }
  }
  .svg-icon-phone {
    @media (max-width: 600px) {
      width: 12px;
      height: 17px;
    }
  }
}

.check-row {
  display: flex;
  flex-wrap: wrap;
  margin: -20px -5px 0;
  .check-wrap {
    margin: 20px 5px 0;
    @media (max-width: 480px) {
      flex: 1 1 auto;
      label{
        width: 100%;
      }
    }
    & + .check-wrap {
      margin-top: 20px;
    }
  }
  &--centered {
    justify-content: center;
    margin: -20px -10px 0;
    .check-wrap {
      margin: 20px 10px 0;
    }
  }
}

.check-grid {
  @extend .check-row;
  .check-wrap {
    width: calc(16.6666% - 10px);
    @media (max-width: 1000px) {
      width: calc(20% - 10px);
    }
    @media (max-width: 768px) {
      width: calc(25% - 10px);
    }
    @media (max-width: 600px) {
      width: calc(50% - 10px);
    }
    .modal & {
      width: calc(20% - 10px);
    }
  }
  &--3col {
    .check-wrap {
      width: calc(33.3333% - 10px);
      @media (max-width: 768px) {
        width: calc(50% - 10px);
      }
      @media (max-width: 600px) {
        width: calc(100% - 10px);
      }
      .modal & {
        width: calc(33.3333% - 10px);
      }
    }

  }
}

.check-wrap {
  line-height: 0;
  label {
    margin-bottom: 0;
  }
  .lng-level-select {
    width: 70px;
    margin-left: 36px;
    margin-top: 2px;
  }
}

.check-wrap + .check-wrap {
  margin-top: 8px;
}

.check-wrap + .check-wrap--switch {
  margin-top: 20px;
}

.group-inputs {
  margin-bottom: 40px;
  &:last-child {
    margin-bottom: 0;
  }
  &__title {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 20px;
  }
}

.select-wrap {
  display: flex;
  align-items: center;
  position: relative;
  padding: 3px 18px 0;
  border-radius: 20px 20px 0 0;
  border: 1px solid transparent;
  border-bottom: none;
  &._opened {
    border-color: $border-color;
  }
  label {
    font-size: 14px;
    color: $text-color-light;
    margin-right: 6px;
    white-space: nowrap;
  }
}

._disabled {
  input,textarea, select, .cmplx-select, .cmplx-input {
    pointer-events: none;
    opacity: .5;
    filter: grayscale(1)
  }
}

.wide-select {
  width: 560px;
  max-width: 100%;
}

.form-section {
  margin-bottom: 40px;
  &:last-child {
    margin-bottom: 0;
  }
  &__title {
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 30px;
    @media (max-width: 480px) {
      font-size: 16px;
      margin-bottom: 20px;
    }
    &--sm-margin {
      margin-bottom: 20px;
    }

  }
}

.form-note {
  font-size: 14px;
  color: $text-color-light;
  @media (max-width: 480px) {
    font-size: 12px;
  }
}

.related {
  display: none!important;
  &._visible {
    display: block!important;
  }
}

label.lg-label {
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 20px;
  @media (max-width: 480px) {
    font-size: 16px;
  }
}

.required-label {
  color: $text-color-light;
}

.form-footer {
  display: flex;
  align-items: center;
  margin: -30px -15px 0;
  flex-wrap: wrap;
  @media (max-width: 480px) {
    margin: -20px -15px 0;
    justify-content: center;
  }
  .form-section + & {
    margin-top: -40px;
  }
  & > * {
    margin: 30px 15px 0;
    @media (max-width: 480px) {
      margin: 20px 15px 0;
    }
  }
  &__note {
    font-size: 14px;
    color: $text-color-light;
    @media (max-width: 480px) {

    }
  }
}

.input-wrap{
  &--unit {
    display: flex;
    align-items: center;
    .unit {
      font-weight: 500;
      margin-left: 10px;
    }
  }
  &--half {
    width: 50%;
  }
  &--fourth {
    width: 25%;
    @media (max-width: 480px) {
      width: 100%;
    }
  }
}


