.page-section {
  margin-top: 120px;
  @media (max-width: 600px) {
    margin-top: 40px;
  }
  &--bg {
    background-color: $bg-color;
    padding-top: 120px;
    padding-bottom: 120px;
    @media (max-width: 600px) {
      padding-top: 40px;
      padding-bottom: 40px;
    }
  }
  &--dark {
    background-color: $theme-color-2;
    padding-top: 120px;
    padding-bottom: 120px;
    color: #ffffff;
    @media (max-width: 600px) {
      padding-top: 40px;
      padding-bottom: 40px;
    }
  }
  &--accent-bg {
    @extend .page-section--dark;
    background-color: $theme-color;
  }
  &--no-top-margin {
    margin-top: 0;
  }
  &--sm-margin {
    margin-top: 60px;
  }
  &--sm-pad {
    padding-top: 80px;
    padding-bottom: 100px;
    @media (max-width: 480px) {
      padding-bottom: 40px;
      padding-top: 40px;
    }
  }
  &--waves {
    padding-bottom: 260px;
    background-image: url('../images/waves.png');
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: 100% auto;
    @media (max-width: 768px) {
      padding-bottom: 180px;
    }
    @media (max-width: 540px) {
      padding-bottom: 100px;
    }
    & + .page-section {
      margin-top: 60px;
    }
  }
  &__inner {
    @extend .container;
  }
  &__title {
    text-align: center;
    margin-bottom: 50px;
    @media (max-width: 600px) {
      margin-bottom: 30px;
    }
    &--sm-margin {
      margin-bottom: 24px;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px -10px 0;
    & > * {
      margin: 20px 10px 0;
    }
  }
}
