.test-tool {
  width: 100%;
  &__media {
    border-radius: 10px;
    height: 240px;
    background-color: $bg-color;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    margin-bottom: 30px;
    @media (max-width: 480px) {
      margin-bottom: 10px;
      height: 100px;
    }
  }
  &__video {
    background-color: $theme-color-2;
    width: 100%;
    height: 100%;
    position: relative;
  }
  &__footer {
    margin-top: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 480px) {
      flex-wrap: wrap;
      margin-top: 14px;
    }
    .test-tool--row & {
      @media (min-width: 1001px) {
        margin-top: 0;
        margin-left: 40px;
      }
      @media (max-width: 1000px) {
        width: 100%;
        margin-top: 22px;
      }
      @media (max-width: 480px) {
        flex-wrap: wrap;
        margin-top: 14px;
      }
    }
  }
  &__note {
    font-size: 14px;
    line-height: 1.3;
    color: $text-color-light;
    width: 60%;
    margin-right: 57px;
    @media (max-width: 480px) {
      width: 100%;
      font-size: 12px;
    }
  }
  &__param {
    font-size: 14px;
    line-height: 1;
    display: inline-flex;
    align-items: center;
    @media (max-width: 480px) {
      margin-top: 10px;
    }
    &:before {
      content: '';
      display: inline-block;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background-color: $theme-color;
      margin-right: 8px;
    }
  }
  &__play-btn {
    min-width: 136px;
    @media (max-width: 480px) {
      width: 100%;
      margin-top: 16px;
    }
  }
  &--row {
    display: flex;
    align-items: center;
    @media (max-width: 1000px) {
      flex-wrap: wrap;
    }
    .test-tool__device{
      width: 62%;
      @media (max-width: 1000px) {
        width: 100%;
      }
    }
  }
}
