.svg-icon{
  display: inline-block;
  fill: $theme-color;
  transition: fill .25s, transform .25s, filter .25s;
}

.svg-sprite-wrap{
  svg{
    position: absolute;
    width: 0;
    height: 0;
    overflow: hidden;
  }
}

.icon-wrap {
  display: inline-flex;
  align-items: flex-start;
  justify-content: flex-start;
}
