.modal {
  position: fixed;
  overflow: auto;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  max-height: 100vh;
  opacity: 0;
  visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  //padding-top: 76px;
  background-color: rgba(27,27,27,.5);
  transform: scale(1.1);
  transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;
  z-index: 9990;
  &[data-level="2"] {
  z-index: 9991;
  }
  @media (max-width: 600px) {
    font-size: 12px;
  }
  &._opened{
    opacity: 1;
    visibility: visible;
    transform: scale(1);
    transition: visibility 0s linear 0s, opacity 0.25s 0s, transform 0.25s;
  }
  &__title {
    font-size: 36px;
    font-weight: 500;
    line-height: 1.185;
    margin-bottom: 40px;
    color: #ffffff;
    .modal--light & {
      color: $text-color-base;
    }
    @media (max-width: 600px) {
      font-size: 24px;
      margin-bottom: 20px;
    }
  }
  &__section-title {
    font-size: 18px;
    margin-bottom: 23px;
  }
  &__desc{
    font-size: 18px;
    margin-bottom: 40px;
    @media (max-width: 540px) {
      font-size: 14px;
      margin-bottom: 20px;
    }
  }
  &__close-btn {
    @extend .btn;
    width: 30px;
    height: 30px;
    position: absolute;
    right: 30px;
    top: 30px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    transition: all .25s;
    z-index: 4;
    @media (max-width: 600px) {
      width: 20px;
      height: 20px;
      right: 20px;
      top: 20px;
    }
    @media (max-width: 480px) {
      width: 16px;
      height: 16px;
    }
    &:hover {
      .svg-icon {
        fill: #ffffff;

      }
    }
    .svg-icon {
      fill: #8FBAD7;
      pointer-events: none;

    }
    .modal--light & {
      width: 22px;
      height: 22px;
      @media (max-width: 480px) {
        width: 16px;
        height: 16px;
        right: 18px;
        top: 18px;
      }
      .svg-icon {
        fill: $theme-color-2;
      }
    }
    .modal--chat & {
      right: 10px;
      top: 10px;
      @media (max-width: 540px) {
        top: 25px;
      }
      .svg-icon {
        fill: $theme-color-2;
      }
      &:hover {
        .svg-icon {
          fill: $theme-color;
        }
      }
    }
  }
  &__inner {
    width: 610px;
    max-width: calc(100% - 40px);
    max-height: 100%;
    position: relative;
    color: #8FBAD7;
    .modal--light & {
      color: $text-color-base;
    }
    .modal--wide & {
      width: 820px;
    }
    .modal--md-wide & {
      width: 914px;
    }
    .modal--lg-wide & {
      width: 1030px;
    }
    .modal--ultrawide & {
      width: 1240px;
    }
    .modal--chat & {
      width: 1030px;
      color: $text-color-base;
    }

  }
  &__submit {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 40px;
    @media (max-width: 600px) {
      margin-top: 20px;
    }
    @media (max-width: 540px) {
      flex-wrap: wrap;
    }
    .accent-btn {
      @media (max-width: 600px) {
        height: 44px;
        font-size: 16px;
        border-radius: 23px;
      }

    }
    .guarantee {
      font-size: 14px;
      margin-left: auto;
      width: 180px;
      @media (max-width: 540px) {
        margin-left: 0;
        margin-top: 20px;
      }
      img {
        margin-right: 7px;
      }
    }
  }
  &__body {
    padding: 80px 75px 70px;
    background-color: $theme-color;
    .modal--light & {
      background-color: #fff;
    }
    @media (max-width: 600px) {
      padding: 30px;
    }
    @media (max-width: 440px) {
      padding: 20px;
    }
  }
  &__footer {
    padding: 32px 75px 30px;
    color: $text-color-light;
    background-color: $theme-color-2;
    @media (max-width: 600px) {
      padding: 20px 30px;
    }
    a{
      color: #ffffff;
      &:hover {
        color: rgba(255,255,255,.75);
      }
    }
  }
  &--chat {

  }
}

form.modal__form {
  input,textarea {
    @media (max-width: 600px) {
      font-size: 12px;
    }
  }
  label {
    @media (max-width: 600px) {
      font-size: 12px;
    }
  }
  .text-radio-row {
    @media (max-width: 540px) {
      flex-wrap: wrap;
    }
    .check-wrap {
      &:first-child {
        label {
          @media (max-width: 540px) {
            border-radius: 23px 23px 0 0;
          }
        }
      }
      &:last-child {
        label {
          @media (max-width: 540px) {
            border-radius: 0 0 23px 23px;
          }
        }
      }
    }
  }
}

.modal:not(.modal--light) form.modal__form {
  input,textarea {
    border-color: #8FBAD7;
    color: #ffffff;
    @include input-placeholder {
      color: #ffffff;
    }
    &:hover,
    &:focus {
      border-color: #ffffff;
    }
  }
  .ss-main .ss-single-selected {
    //border-color: #8FBAD7;
    color: #ffffff;
    border-radius: 30px;
    height: 56px;
    background-color: transparent;
    padding: 0 30px 0 60px;
    font-size: 14px;
    transition: none;
    &.ss-open-below,
    &.ss-open-above {
      background-color: #fff;
      border-radius: 30px 30px 0 0;

      .placeholder,
      .placeholder .ss-disabled {
        color: $text-color-base;
      }
    }
  }
  .ss-main.icon-select .ss-single-selected {
    padding-left: 30px;
  }
  .ss-main .ss-single-selected .placeholder .ss-disabled,
  .ss-main .ss-single-selected .placeholder{
    color: #ffffff;
  }
  .ss-main .ss-single-selected .ss-arrow span.arrow-down, .ss-main .ss-single-selected .ss-arrow span.arrow-up {
    border-width: 4px 5px 0 5px;
    border-color: #ffffff transparent transparent transparent;
  }

  .icon-label {
    .svg-icon {
      fill: #8FBAD7;
    }
  }
  .icon-input {
    &:focus {
      & + .icon-label {
        .svg-icon {
          fill: #ffffff;
        }
      }
    }
  }
  input._filled {
    border-color: #ffffff;
    & + label {
      .svg-icon {
        fill: #ffffff;
      }
    }
  }
  .text-radio-row {
    @media (max-width: 540px) {
      flex-wrap: wrap;
    }
    .check-wrap {
      label {
        border-left: 1px solid #ffffff;
      }
    }
  }
}

.modal:not(.modal--light) {
  .modal__inner {
    a{
      color: #ffffff;
      &:hover {
        color: rgba(255,255,255,.75);
      }
    }
  }
}

.social-auth {
  display: flex;
  align-items: center;
  @media (max-width: 540px) {
    margin-top: 20px;
  }
  &__title {
    margin-right: 16px;
    line-height: 1.185;
  }
  &__links {
    display: flex;
    margin: 0 -5px;
  }
  &__link {
    width: 36px;
    height: 36px;
    border: 1px solid #8FBAD7;
    border-radius: 50%;
    margin: 0 5px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    transition: background-color .25s, border-color .25s;
    &:hover {
      background-color: $theme-color-2;
      border-color: transparent;
    }
    .svg-icon {
      fill: #ffffff;
    }
  }
}

.modal-faq {
  .tabs {
    @media (min-width: 1201px) {
      position: absolute;
      top: 92px;
      right: 40px;
    }
  }
}



