.review {
  position: relative;
  background-color: $bg-color;
  border-radius: 10px;
  padding: 25px 30px 30px;
  font-size: 14px;
  &__top {
    display: flex;
    align-items: center;
    padding-bottom: 20px;
    border-bottom: 1px solid $border-color;
    margin-bottom: 30px;
  }
  &__name {
    margin-right: auto;
    font-size: 18px;
    font-weight: 500;
  }
  &__phone {
    line-height: 1;
    .svg-icon {
      margin-right: 7px;
      position: relative;
      top: 2px;
    }
  }
  &__date {
    margin-left: 56px;
    color: $text-color-light;
    line-height: 1;
  }
  &__close-btn {
    @extend .btn;
    width: 20px;
    height: 20px;
    margin-left: 84px;
    &:hover {
      .svg-icon {
        fill: $theme-color;
      }
    }
    .svg-icon {
      fill: $text-color-light;
    }
  }
  &__body {
    .toggle-text-btn {
      font-size: 14px;
    }
  }
}

.reviews {
  .review {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}
