.main-menu-holder {
  width: 100%;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background-color: $theme-color;
  color: #ffffff;
  padding: 10px 40px 30px;
  display: none;
  @media (max-width: 600px) {
    padding-left: 20px;
    padding-right: 20px;
  }
  &._visible{
    display: block;
    animation: fade-in;
    animation-duration: .25s;
    animation-fill-mode: forwards;
  }
  &._hidden{
    animation: fade-out;
    animation-duration: .25s;
    animation-fill-mode: forwards;
  }
}

.main-menu-controls {
  display: flex;
  align-items: center;
  margin-top: 5px;
  @media (min-width: 741px) {
    display: none;
  }
  @media (max-width: 400px) {
    flex-wrap: wrap;
  }
  .auth-btn--signup {
    background-color: #fff;
    color: $text-color-base;
    margin: 0 auto 0 0;
    @media (max-width: 400px) {
      height: 44px;
      border-radius: 22px;
      font-size: 16px;
      width: 100%;
      margin-bottom: 20px;
      //margin-top: 5px;
    }
  }
  .lng-select {
    @media (max-width: 400px) {
      margin-left: auto;
    }
  }

  & + .guarantee {
    margin-top: 20px;
    @media (min-width: 541px) {
      display: none;
    }
  }
}

.main-menu-wrap {
  @extend .container;
  display: flex;
  align-items: center;
  @media (max-width: 1600px) {
    max-width: none;
    width: auto;
    padding: 0;
    margin: 0;
  }
  .guarantee {
    min-width: 204px;
    margin-top: -20px;
    @media (max-width: 1600px) {
      margin-left: auto;
    }
    @media (max-width: 540px) {
      display: none;
    }
  }

}

.main-menu{
  column-count: 2;
  column-gap: 30px;
  @media (max-width: 1600px) {
    flex: 1 1 auto;
    column-gap: 20px;
  }
  @media (max-width: 400px) {
    column-count: unset;
  }
  &__item{
    width: 300px;
    font-size: 16px;
    margin-bottom:20px;
    @media (max-width: 1600px) {
      width: auto;
    }
    @media (max-width: 540px) {
      margin-bottom: 24px;
    }
    &:nth-child(2n + 1) {

    }
  }
  &__link {
    color: #ffffff;
    &:hover {
      color: #dedede;
    }
  }
}

.menu-btn{
  display: inline-flex;
  align-items: center;
  color: #ffffff;
  .hamburger {
    @media (min-width: 751px) {
      margin-right: 16px;
    }
  }
  & > span {
    &:nth-child(2) {
      @media (max-width: 750px) {
        display: none;
      }
    }
  }
  &:hover {
    .hamburger {
      span:nth-child(2) {
        @media (min-width: 1025px) {
          width: 30px;
        }
      }
    }
  }
}

.secondary-menu{
  column-count: 3;
  column-gap: 30px;
  margin-bottom: -20px;
  @media (max-width: 1024px) {
    column-count: 2;
  }
  @media (max-width: 540px) {
    margin-bottom: -10px;
  }
  &__item{
    font-size: 14px;
    margin-bottom:20px;
    @media (max-width: 540px) {
      font-size: 12px;
      margin-bottom: 10px;
    }
  }
  &__link {
    color: #ffffff;
    &:hover {
      color: #dedede;
    }
  }
}

.crumbs {
  background-color: #FDFDFD;
  height: 56px;
  display: flex;
  align-items: center;
  position: relative;
  @media (max-width: 480px) {
    height: 50px;
    font-size: 12px;
  }
  &__link {
    color: $text-color-base;
    position: relative;
    z-index: 1;
  }
  &__title {
    @media (min-width: 701px) {
      position: absolute;
      left: 0;
      right: 0;
      //top: 0;
      //bottom: 0;
      margin: 0 auto;
      text-align: center;
    }
    @media (max-width: 700px) {
      margin: 0 0 0 auto;
    }
  }
}
