.tutor-info {
  display: flex;
  align-items: flex-start;
  margin-bottom: 60px;
  @media (max-width: 768px) {
    flex-wrap: wrap;
    margin-bottom: 50px;
  }
  @media (max-width: 480px) {
    margin-bottom: 30px;
  }
  &__media {
    flex: 1 1 auto;
    background-color: #1B1B1B;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    cursor: pointer;
    width: 100%;
    &:hover {
      .tutor-info__play {
        border-color: transparent transparent transparent $theme-color;
      }
    }
  }
  &__video-screen {
    opacity: .2;
  }
  &__play {

  }
  &__data {
    width: calc(295px + 50px);
    flex: 0 0 auto;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
    padding-left: 50px;
    @media (max-width: 768px) {
      width: 100%;
      padding: 0;
      margin-top: 50px;
    }
    @media (max-width: 480px) {
      margin-top: 20px;
    }
  }
  &__title {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    @media (max-width: 480px) {
      margin-bottom: 14px;
    }
  }
  &__name {
    font-size: 36px;
    font-weight: 500;
    @media (max-width: 480px) {
      font-size: 24px;
    }
  }
  &__rating {
    margin-left: 20px;
  }
  &__location {
    margin-bottom: 30px;
    @media (max-width: 480px) {
      font-size: 14px;
    }
    img{
      @media (max-width: 480px) {
        width: 20px;
        height: 20px;
        margin-right: 7px;
      }
    }
  }
  &__params {
    width: 100%;
    margin-bottom: 30px;
    font-size: 16px;
    @media (max-width: 480px) {
      font-size: 14px;
      margin-bottom: 20px;
    }
  }
  &__rate {
    display: flex;
    align-items: center;
    @media (max-width: 480px) {
      font-size: 14px;
    }
    .rate {
      @media (max-width: 768px) {
        flex: 1 1 100%;
      }
      @media (max-width: 480px) {
        height: 36px;
      }
    }
    .rate-level {
      margin-left: 30px;
      flex: 0 0 auto;

    }
  }
  &__btns {
    margin-top: auto;
    @media (max-width: 768px) {
      display: flex;
      align-items: center;
      margin: 10px -10px 0;
    }
    @media (max-width: 440px) {
      flex-wrap: wrap;
    }
    & > * {
      margin-top: 20px;
      @media (max-width: 768px) {
        margin: 20px 10px 0;
      }
      @media (max-width: 440px) {
        width: 100%;
      }
    }
  }
}

.tutor-desc{
  padding-right: 345px;
  @media (max-width: 768px) {
    padding: 0;
    //margin-top: 50px;
  }
}

.tutor-teaser {
  padding: 40px;
  border-radius: 10px;
  background-color: $bg-color;
  display: flex;
  //align-items: center;
  margin-bottom: 40px;
  @media (max-width: 540px) {
    padding: 20px;
    margin-bottom: 20px;
  }
  @media (max-width: 440px) {
    flex-direction: column;
  }
  &__ava {
    border-radius: 50%;
    margin-right: 20px;
    @media (max-width: 540px) {
      width: 50px;
      height: 50px;
      margin-right: 14px;
    }
  }
  &__top {
    display: flex;
    align-items: center;
  }
  &__header {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
  }
  &__name {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 13px;
    @media (max-width: 540px) {
      font-size: 16px;
      margin-bottom: 9px;
    }
  }
  &__level {
    border-left: 1px solid $border-color;
    padding-left: 20px;
    margin-left: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    @media (max-width: 440px) {
      margin-left: 0;
      padding-left: 0;
      padding-top: 14px;
      margin-top: 20px;
      border-left: 0;
      border-top: 1px solid $border-color;
      flex-direction: row;
      align-items: flex-start;
      justify-content: flex-start;
    }
  }
  &__reviews {
    margin-top: 14px;
    color: $text-color-light;
    @media (max-width: 440px) {
      margin: 0 0 0 18px;
    }
  }
}

.tutor-brief {
  display: flex;
  align-items: center;
  @media (max-width: 480px) {
    font-size: 12px;
  }
  &__ava {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin-right: 20px;
    .tutor-brief--column & {
      @media (min-width: 481px) {
        margin-right: 0;
        margin-bottom: 20px;
      }
    }
  }
  &__img {
    border-radius: 50%;
    @media (max-width: 480px) {
      width: 60px;
      height: 60px;
    }
  }
  &__rating {
    margin-top: -20px;
    @media (max-width: 480px) {
      height: 24px;
      border-radius: 18px;
    }
  }
  &__info {
    .tutor-brief--column & {
      @media (min-width: 481px) {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
      }
    }
  }
  &__name {
    font-size: 18px;
    font-weight: 500;
    line-height: 1;
    @media (max-width: 480px) {
      font-size: 16px;
    }
  }
  &__reviews {
    font-size: 14px;
    color: $text-color-light;
    line-height: 1;
    margin-top: 10px;
    @media (max-width: 480px) {
      font-size: 12px;
      margin-top: 5px;
    }
  }
  &__location {
    margin-top: 10px;
    .tutor-brief--column & {
      @media (min-width: 481px) {
        margin-top: 20px;
      }
    }
  }
  &--column {
    @media (min-width: 481px) {
      width: 100%;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
    }
  }
}

.tutor-choice {
  padding-top: 60px;
  padding-bottom: 50px;
  @media (max-width: 700px) {
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .check-row {
    @media (min-width: 701px) {
      flex-wrap: nowrap;
    }
    .check-wrap {
      width: calc(400px - 20px);
      @media (max-width: 1000px) {
        width: calc(100% - 20px);
      }
    }
  }
}

.inline-ava {
  display: inline-block;
  border-radius: 50%;
  margin: -10px 0 0 10px;
}

.inline-tutor {
  white-space: nowrap;
}

.tutor-payments {
  display: flex;
  width: 100%;
  @media (max-width: 1000px) {
    align-items: flex-start;
  }
  @media (max-width: 700px) {
    flex-wrap: wrap;
  }
  &__main {
    margin-right: 50px;
    flex: 1 1 auto;
    @media (max-width: 700px) {
      width: 100%;
      order: 1;
      margin-top: 20px;
      margin-right: 0;
    }
  }
  &__sidebar {
    width: 240px;
    flex: 0 0 240px;
    background-color: $bg-color;
    border-radius: 10px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @media (max-width: 1000px) {
      padding-top: 50px;
      padding-bottom: 50px;
      width: 200px;
      flex: 0 0 200px;
    }
    @media (max-width: 700px) {
      width: 100%;
      flex: 1 1 100%;
      order: 0;
      padding: 20px;
      border-radius: 10px;
    }
  }
  &__header {
    padding-bottom: 40px;
    margin-bottom: 40px;
    border-bottom: 1px solid $border-color;
    @media (max-width: 700px) {
      padding-bottom: 20px;
      margin-bottom: 20px;
    }
  }
  &__title {
    font-size: 24px;
    font-weight: 500;
    line-height: 1.3;
    margin-bottom: .65em;
    @media (max-width: 700px) {
      font-size: 16px;
    }
  }
  &__sub-title {
    font-size: 18px;
    line-height: 1.3;
    color: $text-color-light;
    @media (max-width: 700px) {
      font-size: 16px;
    }
  }

}

.payment-features {
  display: flex;
  @media (max-width: 1000px) {
    flex-wrap: wrap;
  }
  &__item {
    display: flex;
    flex: 1 1 auto;
    @media (max-width: 1000px) {
      width: 100%;
      margin-bottom: 30px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    @media (max-width: 700px) {
      margin-bottom: 20px;
    }
 }

  &__icon-wrap {
    width: 30px;
    margin-right: 14px;
    flex-shrink: 0;
  }
  &__body{

  }
  &__label {
    font-size: 14px;
    line-height: 1.35;
    margin-bottom: 8px;
    @media (max-width: 700px) {
      margin-bottom: 4px;
    }
  }
  &__value {
    font-size: 18px;
    font-weight: 500;
    line-height: 1.35;
    img {
      margin: -7px 4px 0 0;
    }
    span {
      display: inline-block;
      margin-right: .5em;
    }
  }
  &__note {
    font-size: 14px;
    line-height: 1.25;
    color: $text-color-light;
    max-width: 230px;
    margin-top: 15px;
    @media (max-width: 700px) {
      margin-top: 8px;
    }
  }
}

.balance {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  @media (max-width: 700px) {
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
  }
  &__icon-wrap{
    margin-bottom: 15px;
    @media (max-width: 700px) {
      margin: 0 12px 0 0;
      width: 30px;
      .svg-icon {
        width: 30px;
        height: 44px;
      }
    }
  }
  &__label {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 15px;
    line-height: 1;
    @media (max-width: 700px) {
      font-size: 16px;
      margin-bottom: 10px;
    }
  }
  &__value {
    font-size: 34px;
    font-weight: 500;
    line-height: 1;
    color: $theme-color;
    @media (max-width: 700px) {
      font-size: 27px;
    }
  }
}

.payment-frm {
  width: 100%;
  display: flex;
  align-items: center;
  @media (max-width: 700px) {
    flex-wrap: wrap;
  }
  &__select {
    flex: 1 1 auto;
    @media (max-width: 700px) {
      width: 100%;
    }
  }
  &__submit {
    width: 120px;
    flex: 0 0 120px;
    margin-left: 20px;
    @media (max-width: 700px) {
      width: 100%;
      flex: 1 1 auto;
      margin: 20px 0 0 0;
    }
  }
}

.user-header {
  display: flex;
  align-items: center;
  font-size: 36px;
  line-height: 1;
  &__ava {
    margin-right: 20px;
    border-radius: 50%;
    width: 80px;
    height: 80px;
  }
  &__approve-btn {
    margin-left: 30px;
  }
}

.popup-profile {
  font-size: 14px;
  .toggle-text-btn {
    font-size: 14px;
  }
  .schedule-wrap {
    margin-top: 90px;
  }
}

.popup-profile-desc {
  margin-top: 40px;
}

.video {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
}

.video__media {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

img.video__media {
  opacity: .2;
}




