.success-icon {
  width: 104px;
  height: 104px;
  background-color: $theme-color-4-light;
  border: 7px solid $bg-color;
  background-image: url("../images/tick-white-green-shadow.svg");
  background-position: 30px 30px;
  background-repeat: no-repeat;
  border-radius: 50%;
}

.thnx {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding-bottom: 60px;
  padding-top: 60px;
  @media (max-width: 480px) {
    padding-bottom: 40px;
    padding-top: 40px;
  }
  &--full {
    flex: 1 1 auto;
    padding-bottom: 0;
  }
  &__icon{
    margin-bottom: 36px;
    @media (max-width: 480px) {
      margin-bottom: 20px;
      width: 60px;
      height: 60px;
      border-width: 4px;
      background-size: 70%;
      background-position: 17px 18px;
    }
  }
  &__title {
    font-size: 44px;
    line-height: 1.25;
    font-weight: 500;
    text-align: center;
    @media (max-width: 480px) {
      font-size: 30px;
    }
  }
  &__desc {
    max-width: 460px;
    font-size: 18px;
    line-height: 1.5;
    margin: 30px auto;
    text-align: center;
    @media (max-width: 480px) {
      font-size: 16px;
      margin-top: 20px;
    }
  }
  &__back-link {
    margin-top: auto;
  }
}
