.tabs {
  display: flex;
  flex-wrap: wrap;
  margin: -20px -20px 40px;
  &--nowrap {
    flex-wrap: nowrap;
  }
  &--btns {
    margin: 0 0 40px;
    @media (max-width: 480px) {
      margin-bottom: 20px;
    }
  }
  @media (max-width: 480px) {
    margin-top: -10px;
    margin-bottom: 20px;
  }
  &__item {
    display: inline-flex;
    margin: 20px 20px 0;
    color: $text-color-light;
    .tabs--btns & {
      height: 50px;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      background-color: #fff;
      color: $text-color-base;
      flex: 1 1 auto;
      margin: 0;
      border-right: 1px solid $border-color;
      @media (max-width: 480px) {
        height: 40px;
        font-size: 12px;
      }
      &:first-child {
        border-radius: 10px 0 0 10px;
      }
      &:last-child {
        border-radius: 0 10px 10px 0;
        border-right-color: transparent;
      }
      &._current {
        background-color: $theme-color;
        color: #ffffff;
        border-right-color: transparent;
      }
      &:not(._current) {
        &:hover {
          cursor: pointer;
          color: $theme-color;
        }
      }
    }
    .tabs--bg & {
      background-color: $bg-color;
    }
    @media (max-width: 480px) {
      width: 100%;
      margin-top: 10px;
    }
    .svg-icon {
      margin-right: 14px;
      fill: $text-color-light;
    }
    &._current {
      color: $theme-color;
      .svg-icon {
        fill: $theme-color;
      }
    }
    &:not(._current){
      cursor: pointer;
      &:hover {
        color: $theme-color;
        .svg-icon {
          fill: $theme-color;
        }
      }
    }
  }
}

.box{
  display: none;
  &._visible{
    display: block;
  }
}
