div.hidden-content {
  display: none;
}

.toggle-hidden-btn {
  @extend .btn;
  color: $text-color-light;
  font-size: 14px;
  transition: color .25s;
  &:hover {
    color: $theme-color;
  }
  &:after {
    content: '';
    display: inline-block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 3px 3px 0 3px;
    border-color: $text-color-light transparent transparent transparent;
    margin-left: 4px;
    //transition: all .25s;
  }
  &._active {
    &:after {
      transform: scaleY(-1);
    }
  }
}

.toggle-text-btn {
  @extend .btn;
  display: inline-block;
  color: $text-color-light;
}

a.toggle-text-btn {
  display: inline;
  margin-left: .25em;
  font-size: inherit;
}
