.dropdown {
  height: 36px;
  list-style: none;
  padding: 12px 0 0;
  border-radius: 18px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  cursor: pointer;
  position: relative;
  flex-shrink: 0;
  &--dark {
    &:hover {
      background-color: $theme-color-2;
    }
  }
  &:hover {
    border-radius: 18px 18px 0 0;
    @media (max-width: 740px) {
      background-color: #fff;
    }
    .dropdown__dropdown {
      display: block;
    }
    .dropdown__item{
      &._current {
        color: $theme-color;
        &:after {
          border-color: $theme-color transparent transparent transparent;
          transform: scaleY(-1);
          margin-top: -1px;
        }

        //@media (max-width: 740px) {
        //  color: #ffffff;
        //  &:after {
        //    border-color: #ffffff transparent transparent transparent;
        //  }
        //}
      }
    }
  }
  &__dropdown {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    margin-top: -1px;
    display: none;
    background-color: #ffffff;
    border-radius: 0 0 18px 18px;
    padding-bottom: 12px;
    z-index: 2;
    .dropdown--dark & {
      background-color: $theme-color-2;
    }
    .dropdown__item {
      margin-bottom: 9px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &._opened {

  }
  &__item {
    padding: 0 16px 0 14px;
    line-height: 1;
    &._current {
      position: relative;
      display: flex;
      align-items: center;
      &:after {
        content: '';
        display: inline-block;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 3px 3px 0 3px;
        border-color: $text-color-base transparent transparent transparent;
        margin-left: 4px;
        //transition: all .25s;
      }
      .dropdown--dark & {
        &:after {
          border-color: #ffffff transparent transparent transparent;
        }
      }
    }
  }
  &__link {
    color: $text-color-base;
    .dropdown--dark & {
      color: #ffffff;
    }
    &:hover {
      color: $link-color-hover;
    }
  }

  &--icon {
    width: 36px;
    height: 36px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    &:before {
      content: '';
      width: 100%;
      height: 100%;
      border-radius: 50%;
      border: 1px solid $border-color;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
    }
    &.dropdown--dark {
      &:before {
        border-color: rgba(255,255,255,.3);
      }
    }
    &:hover {
      //border-color: transparent;
      &:before {
        display: none;
      }
    }
    .dropdown__dropdown {
      padding: 0 0 7px;
    }
    .dropdown__item {
      padding: 0 7px;
      margin: 6px 0 0;
      &:first-child {
        margin-top: 0;
      }
      &._current {
        padding: 0;
        margin: 0;
      }
      &:after {
        display: none;
      }
    }
  }
}
