.location {
  color: $text-color-light;
  display: inline-flex;
  align-items: center;
  line-height: 1;

  img {
    width: 20px;
    height: 20px;
    margin-right: 7px;
    @media (max-width: 640px) {
      width: 16px;
      height: 16px;
    }
  }
  &--lg {
    font-size: 16px;
    img {
      width: 26px;
      height: 26px;
      margin-right: 12px;
    }
  }
}
