.lg-size {
  font-size: 18px;
}
.text-logo {
  white-space: nowrap;
  .sign {
    margin: 0 0 0 .15em;
  }
}

.text-light {
  color: $text-color-light;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.text-highlight {
  color: $theme-color;
}

.hidden {
  display: none!important;
}

.group:after{
  content:".";
  display:block;
  clear:both;
  height:0
}

.mobile-text {
  @media (min-width: 400px) {
    display: none;
  }
  & + * {
    @media (max-width: 400px) {
      display: none;
    }
  }
}
