.page-header {
  background-color: $theme-color-2;
  color: #ffffff;
  z-index: 7;
  position: relative;
  &:not(.page-header--bordered) {
    &:before {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      background-color: $theme-color-2;
      z-index: 1;
      transition: background-color .25s;
    }
  }
  &__top {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 76px;
    padding: 0 40px;
    z-index: 6;
    transition: background-color .25s;
    @media (max-width: 750px) {
      justify-content: flex-start;
    }
    @media (max-width: 600px) {
      height: 60px;
      padding: 0 20px;
    }
    .page-header--bordered & {
      &:after {
        content: '';
        display: block;
        width: calc(100% - 80px);
        height: 1px;
        background-color: rgba(255,255,255,.2);
        position: absolute;
        left: 40px;
        bottom: 0;
        transition: opacity .25s;
      }
    }
    ._active & {
      background-color: $theme-color;
      &:after {
        opacity: 0;
      }
    }
    .page-header:not(.page-header--member) & > .page-header__controls {
      .auth-btn {
        &--signup {
          @media (max-width: 740px) {
            display: none;
          }
        }
      }
      .lng-select {
        @media (max-width: 740px) {
          display: none;
        }
      }
      .currency-select {
        @media (max-width: 740px) {
          display: none;
        }
      }
    }
  }
  &__inner {
    @extend .container;
    position: relative;
    z-index: 2;
    display: flex;
    align-items: center;
    @media (max-width: 1600px) {
      flex: 1 1 auto;
    }
    @media (max-width: 750px) {
      order: 2;
      width: auto;
      padding: 0;
      margin: 0 0 0 30px;
      flex: 0 0 auto;
    }
    .page-header--member & {
      @media (max-width: 1100px) {
        padding-left: 30px;
      }
      @media (max-width: 480px) {
        display: none;
      }
    }
    .page-header--admin & {
      @media (max-width: 1400px) {
        padding-left: 0;
        padding-right: 0;
      }
    }
  }
  &__lessons {
    display: flex;
    align-items: center;
    font-size: 14px;
    margin-left: 88px;
    @media (max-width: 1600px) {
      margin-left: 60px;
    }
    @media (max-width: 1270px) {
      margin-left: 30px;
    }
    @media (max-width: 1100px) {
      margin-left: 20px;
    }
    @media (max-width: 760px) {
      display: none;
    }
  }
  &__lessons-count {
    span {
      &:first-child {
        color: $text-color-light;
        display: inline-block;
        margin-right: 3px;
      }
    }
  }
  &__link {
    color: #ffffff;
    font-size: 14px;
    &:hover {
      color: $text-color-ultralight;
    }
  }
  &__lesson-buy-btn {
    margin-left: 40px;
    @media (max-width: 1270px) {
      margin-left: 30px;
    }
  }
  &__logo {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 123px;
    height: 28px;
    position: absolute;
    left: 40px;
    z-index: 2;
    @media (max-width: 1600px) {
      position: static;
      flex-shrink: 0;
      margin-right: 57px;
    }
    @media (max-width: 750px) {
      margin-right: auto;
      order: 0;
    }
    @media (max-width: 400px) {
      width: 98px;
      height: auto;
    }
    .page-header--member & {
      @media (max-width: 1270px) {
        margin-right: 0;
      }

    }
    .page-header--admin & {
      @media (max-width: 1400px) {
        margin-right: 40px;
      }
    }
    img {
      &:first-child {
        ._active & {
          display: none;
        }
      }
      &:last-child {
        display: none;
        ._active & {
          display: block;
        }
      }
    }
  }
  &__controls {
    display: flex;
    align-items: center;
    position: absolute;
    right: 40px;
    z-index: 2;
    @media (max-width: 1600px) {
      position: static;
    }
    @media (max-width: 750px) {
      margin-left: auto;
      order: 1;
    }
    .page-header--member & {
      @media (max-width: 1600px) {
        position: absolute;
      }
      @media (max-width: 600px) {
        position: static;
        order: 3;
      }
    }

    & > .dropdown {
      &__dropdown {
        z-index: 10;
      }
    }

    .user-controls {
      .page-header--admin & {
        @media (max-width: 1400px) {
          margin-left: 30px;
        }
      }
    }
  }
  &__title {
    font-size: 18px;
    font-weight: 500;
  }
  &__user-id {
    color: $text-color-light;
    margin-left: 32px;
    font-size: 14px;
    line-height: 1;
  }
  &__add-student-btn {
    margin-left: 40px;
    .page-header--admin & {
      @media (max-width: 1400px) {
        margin-left: 30px;
      }
    }
  }
  &__add-teacher-btn {
    margin-left: 20px;
  }
  &__info-data-wrap {
    margin-left: 40px;
    display: flex;
    align-items: center;
    @media (max-width: 1600px) {
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
    }
    @media (max-width: 1400px) {
      margin-left: 20px;
    }
  }
  &__info-data {

  }
  .current-state {
    @media (max-width: 960px) {
      display: none;
    }

  }
  .auth-btn {
    width: 88px;
    min-width: 88px;
    padding: 0 10px;
    margin: 0 0 0 10px;
    @media (max-width: 400px) {
      height: 30px;
      font-size: 12px;
      border-radius: 15px;
      width: 66px;
      min-width: 66px;
    }
  }
  .currency-select{}
  .lng-select {
    margin-left: 10px;
  }

  &--member {
    .page-header__controls {
      & > .currency-select,
      & > .lng-select{
        @media (max-width: 1100px) {
          display: none;
        }
      }
    }
  }


  .info-data-wrap {

  }
}

.info-data {
  margin-left: 42px;
  @media (max-width: 1600px) {
    margin-left: 0;
  }
  &:first-child {
    margin-left: 0;
  }
  span {
    &:first-child {
      color: $text-color-light;
      display: inline-block;
      margin-right: 3px;
    }
  }
}

.info-data-wrap {

}

.lng-select{
  img{
    width: 22px;
    height: 22px;
  }
}

.current-state {
  display: flex;
  align-items: center;
  margin-left: 50px;
  @media (max-width: 1270px) {
    margin-left: 30px;
  }
  .svg-icon {
    margin-right: 10px;
  }
  &__data {
    font-size: 14px;
    line-height: 17px;
  }
  &__date {}
  &__time {
    span {
      &:first-child {
        width: 60px;
        display: inline-block;

      }
      &:last-child {
        color: $text-color-light;
        display: inline-block;
        margin-left: 3px;
      }
    }
  }

  .page-header--admin & {
    @media (max-width: 1400px) {
      margin-left: 30px;
    }
  }
}

.user-controls {
  display: flex;
  align-items: center;
  margin-left: 60px;
  @media (max-width: 1270px) {
    margin-left: 30px;
  }
  &__section {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid $border-color;
    padding: 20px 20px 20px 30px;
    &:first-child {
      @media (min-width: 481px) {
        display: none;
      }
    }
    &:nth-child(2) {
      @media (min-width: 761px) {
        display: none;
      }
    }
    &:nth-child(3) {
      @media (min-width: 1101px) {
        display: none;
      }
    }
    &--sm-pad {
      padding: 10px 20px 10px 30px;
    }
    .dropdown {
      &:first-child {
        margin-left: -12px;
      }
    }
  }
  &__btn {
    @extend .btn;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    border: 1px solid rgba(255,255,255,.3);
    margin-left: 20px;
    flex-shrink: 0;
    @media (max-width: 480px) {
      margin-left: 10px;
    }
    &:first-child {
      margin-left: 0;
    }
    &:hover {
      .svg-icon {
        fill: #ffffff;
      }
    }
    &._has-new {
      position: relative;
      &:before {
        content: '';
        display: inline-block;
        width: 11px;
        height: 11px;
        border: 3px solid $theme-color-2;
        background-color: $theme-color-3;
        position: absolute;
        right: 8px;
        top: 5px;
        border-radius: 50%;
      }
      .svg-icon {
        fill: #ffffff;
      }
      &._active {
        &:before {
          border-color: $theme-color;
        }
      }
    }
    &._active {
      border-color: $theme-color;
      background-color: $theme-color;
      .svg-icon {
        fill: #ffffff;
      }
    }
    .svg-icon {
      fill: $text-color-light;
    }
  }
  &__menu {
    margin-left: 20px;
    position: relative;
    @media (max-width: 480px) {
      margin-left: 10px;
    }
  }
  &__menu-ava {
    display: block;
    border-radius: 50%;
  }
  &__menu-btn {
    @extend .btn;
    width: 14px;
    height: 14px;
    background-color: $theme-color;
    border-radius: 50%;
    position: absolute;
    right: 0;
    bottom: 0;
    &:before {
      content: '';
      display: inline-block;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 3px 3px 0 3px;
      border-color: #ffffff transparent transparent transparent;
      transition: transform .25s;
    }
    &._active {
      &:before {
        transform: scaleY(-1);
      }
    }
  }
  &__menu-holder {
    position: absolute;
    top: 100%;
    right: 0;
    width: 242px;
    padding-top: 20px;
    display: none;
    @media (max-width: 1100px) {
      width: 280px;
    }
  }
  &__menu-inner {
    background-color: #fff;
    box-shadow: $box-shadow-light;
    color: $text-color-base;
  }
  &__menu-list {
    padding: 30px;
  }
  &__menu-item{
    font-size: 14px;
    margin-bottom: 12px;
    &:last-child {
      margin-bottom: 0;
    }
    &--logout {
      margin-top: 24px;
    }
  }
  &__menu-link {
    color: $text-color-base;
    .user-controls__menu-item--logout & {
      color: $text-color-light;
    }
  }
  &__notify {
    position: relative;
  }
  &__notify-holder {
    position: absolute;
    top: 100%;
    right: 0;
    width: 230px;
    padding-top: 20px;
    display: none;
    @media (max-width: 480px) {
      position: fixed;
      width: calc(100% - 40px);
      left: 20px;
      top: 40px;
    }
    .notify-list {
      background-color: #fff;
      padding: 20px;
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
    }
  }
  &__lessons-count {
    font-size: 14px;
    line-height: 1.2;
    span{
      display: block;
      &:first-child {
        color: $text-color-light;
      }
    }
  }
  &__link {
    font-size: 14px;
    color: $text-color-base;
  }
  .dropdown {
    &__dropdown {
      box-shadow: $box-shadow-light;
    }
  }
}

.notify {
  background-color: rgba(85, 183, 110, .1);
  padding: 10px 20px;
  border-radius: 10px;
  font-size: 14px;
  color: $text-color-base;
  &--warning {
    background-color: rgba(228, 82, 53, .1);
  }
  &__meta {
    font-size: 12px;
    line-height: 1;
    color: $text-color-light;
    margin-top: 2px;
  }
}

.notify-list {
  .notify {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.panel {
  &._visible{
    display: block;
    animation: fade-in;
    animation-duration: .25s;
    animation-fill-mode: forwards;
  }
  &._hidden{
    animation: fade-out;
    animation-duration: .25s;
    animation-fill-mode: forwards;
  }
}
