.catalog-controls {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-wrap: wrap;
  .showed {
    margin-left: 37px;
  }
  .search-frm {
    margin-left: 20px;
  }
}

.data-count {
  @extend .btn;
  display: inline-flex;
  align-items: center;
  height: 36px;
  padding: 0 8px 0 20px;
  border-radius: 18px;
  border: 1px solid $border-color;
  font-size: 14px;
  line-height: 1;
  transition: border-color .25s;
  &:hover {
    border-color: $theme-color;
  }
  &__label {

  }
  &__value {
    width: 20px;
    height: 20px;
    color: #ffffff;
    border-radius: 50%;
    background-color: $theme-color;
    font-size: 12px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-left: 11px;
  }
}

.search-frm {
  position: relative;
  width: 295px;
  max-width: 100%;
  &__submit {
    @extend .btn;
    width: 30px;
    height: 30px;
    position: absolute;
    right: 6px;
    top: 2px;
    .svg-icon {
      fill: $text-color-light;
    }
    &:hover {
      .svg-icon {
        fill: $theme-color;
      }
    }
  }
}
input.search-frm__input {
  background-color: #fff;
  height: 36px;
  border-radius: 18px;
  padding: 0 36px 0 20px;
}

.showed {
  font-size: 14px;
  color: $text-color-light;
  span {
    color: $text-color-base;
  }
}
