@function max($numbers...) {
  @return m#{a}x(#{$numbers})
}

@function min($numbers...) {
  @return m#{i}n(#{$numbers})
}

@import '../../../node_modules/slim-select/dist/slimselect';
//@import '../../../node_modules/semantic-ui-dropdown/dropdown.min';
//@import '../../../node_modules/semantic-ui-transition/transition.min';
@import '../../../node_modules/selectize/dist/css/selectize';
@import '../../../node_modules/air-datepicker/dist/css/datepicker.min';
@import '../../../node_modules/glightbox/dist/css/glightbox.min';
@import '../../../node_modules/flickity/dist/flickity';

.goverlay{
  background-color: rgba(27,27,27, .5);
}
.gbtn{
  &.disabled{
    display: none;
  }
}
.glightbox-clean .gslide-media {
  box-shadow: none;
}
.glightbox-clean .gclose, .glightbox-clean .gnext, .glightbox-clean .gprev{
  background-color: transparent;
  svg{
    fill: #ffffff;
  }
}
.glightbox-clean .gclose:hover, .glightbox-clean .gnext:hover, .glightbox-clean .gprev:hover {
  background-color: transparent;
}
.glightbox-clean .gclose{
  opacity: 1;
}

.selectize-input{
  height: 40px;
  padding: 0 30px;
  border-radius: 18px;
  position: relative;
  z-index: 2;
  transition: none;
  cursor: pointer;
  text-overflow: ellipsis;
  border: 1px solid $inputs-border-color;
  box-shadow: none;
  display: flex;
  @media (max-width: 600px) {
    padding: 0 30px 0 20px;
  }
  &.focus {
    box-shadow: none;
  }
  &.dropdown-active {
    border-radius: 18px 18px 0 0;
    border-bottom-color: transparent;
  }
  & > * {
    display: flex;
    align-items: center;
  }
  &:before {
    display: none!important;
  }
  .item {
    color: $text-color-base;
  }

}
.selectize-control.single .selectize-input.input-active {
  display: flex;

}
.selectize-dropdown, .selectize-input, .selectize-input input {
  font-size: 14px;
  color: $text-color-light;

}
.selectize-dropdown .option {
  font-size: 14px;
  padding: 5px 14px;
  border-radius: 15px;
  height: 30px;
  color: $text-color-base;
  display: flex;
  align-items: center;
  cursor: pointer;
  &:hover {
    background-color: #FAFAFA;
    color: #1f75af;
  }
}
.selectize-dropdown-content {
  max-height: 404px;
  padding: 8px 16px 16px;
}
.selectize-dropdown {
  position: absolute;
  z-index: 10;
  border: 1px solid $inputs-border-color;
  background: #fff;
  margin: -1px 0 0 0;
  border-top: 0;
  box-sizing: border-box;
  box-shadow: none;
  border-radius: 0 0 20px 20px;
}
.selectize-control.single .selectize-input:after {
  content: ' ';
  display: block;
  position: absolute;
  top: 50%;
  right: 15px;
  margin-top: 0;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 3px 3px 0 3px;
  border-color: #8C8C8C transparent transparent transparent;
}
.selectize-control.single .selectize-input.dropdown-active:after {
  margin-top: 0;
  border-width: 0 3px 3px 3px;
  border-color: transparent transparent #8C8C8C transparent;
}
.selectize-control.btn-select.single .selectize-input:after {
  content: 'Choose';
  width: 76px;
  height: 30px;
  margin: 0 0 0 10px;
  border-radius: 18px;
  background-color: #1f75af;
  position: absolute;
  right: 4px;
  top: 4px;
  color: #ffffff;
  border: 0;
  transition: background-color .25s;
  font-size: 14px;
  line-height: 1;
  text-align: center;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}
.selectize-control.btn-select.single .selectize-input.dropdown-active:after {
  border: 0;
  margin: 0;
  z-index: 7;
  background-color: #8C8C8C;
}
.selectize-control.multi .selectize-input>div  {
  white-space: nowrap;
}


.ss-single-selected{
  .placeholder{
    color: $text-color-base;
    img + span {
      margin-left: 10px;
    }
  }
}
.ss-content{
  transform: none;
  opacity: 1;
  display: none;
  transition: transform .2s, opacity .2s;
  background-color: $inputs-bg-color;
  border-color: $inputs-border-color;

  .ss-open-above + & {
    margin-top: -1px!important;
    border-top: 0;
    border-radius: 0 0 20px 20px;
    //border-radius: 20px 20px 0 0;
    //border-bottom: 0;
    //.ss-list {
    //  padding-bottom: 10px;
    //}
  }
  .ss-open-below + & {
    border-radius: 0 0 20px 20px;
    border-top: 0;
  }
  &.ss-open{
    display: block;
  }

  .ss-search {
    padding: 10px 30px;
    input {
      width: 100%;
      height: 40px;
      padding: 0 30px;
      border-radius: 18px;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      outline: 0;
      font-size: 14px;
      line-height: normal;
      font-weight: 400;
      border: 1px solid #e7e7e7;
      background-color: transparent;
      transition: border-color .3s;
      &:hover{
        box-shadow: none;
        border-color: $inputs-focus-border-color;
      }
      &:focus{
        box-shadow: none;
        outline: none;
        border-color: $inputs-focus-border-color;
      }
    }
  }
}
.ss-main .ss-single-selected.ss-open-above {
  border-radius: 20px 20px 0 0;
}
.ss-content .ss-list{
  max-height: 404px;
  padding: 8px 16px 16px;
  //background-color: #fff;
  @media (max-width: 600px) {
    padding: 0 6px 10px;
  }
  .ss-option{
    font-size: 14px;
    padding: 5px 14px;
    border-radius: 15px;
    height: 30px;
    color: $text-color-base;
    display: flex;
    align-items: center;
    img + span {
      margin-left: 10px;
    }
  }
}
.ss-content .ss-list .ss-option:hover, .ss-content .ss-list .ss-option.ss-highlighted {
  background-color: $bg-color;
  color: $theme-color;
}
.ss-content .ss-list .ss-option.ss-disabled{
  color: $text-color-light;
  cursor: default;
  &:hover{
    background-color: transparent;
  }
}
.ss-main .ss-single-selected .placeholder .ss-disabled {
  color: $text-color-light;
}
.ss-content .ss-list .ss-option.ss-option-selected{
  color: $theme-color;
  cursor: default;
  background-color: transparent;
  &:hover{
    background-color: transparent;
  }
  &.ss-disabled {
    display: none;
  }
}
.ss-main .ss-single-selected .ss-arrow{
  width: 6px;
  height: 100%;
  margin: 0;
  flex: 0 0 auto;
}
.ss-main.cmplx-select .ss-single-selected {
  padding-right: 85px;
}
.ss-main.cmplx-select .ss-single-selected .ss-arrow{
  width: 76px;
  height: 30px;
  margin: 0 0 0 10px;
  display: inline-block;
  border-radius: 18px;
  background-color: $theme-color;
  position: absolute;
  right: 5px;
  top: 4px;
  color: #ffffff;
  transition: background-color .25s;
  &:before {
    content: 'Choose';
    //color: #ffffff;
    font-size: 14px;
    line-height: 1;
    text-align: center;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
  }
}
.ss-main.cmplx-select .ss-single-selected.ss-open-above .ss-arrow,
.ss-main.cmplx-select .ss-single-selected.ss-open-below .ss-arrow {
  background-color: $text-color-light;
}
.ss-main .ss-single-selected .ss-arrow span.arrow-down,
.ss-main .ss-single-selected .ss-arrow span.arrow-up{
  display: inline-block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 3px 3px 0 3px;
  margin: 0;
  padding: 0;
  border-color: $text-color-light transparent transparent transparent;
  transform: none;
  transition: all .25s;
}
.ss-main.cmplx-select .ss-arrow span.arrow-down,
.ss-main.cmplx-select .ss-arrow span.arrow-up {
  display: none!important;
}
.ss-main .ss-single-selected .ss-arrow span.arrow-up {
  transform: scaleY(-1);
}
.ss-main{
  .ss-single-selected {
    height: 40px;
    font-size: 14px;
    border-radius:20px;
    padding: 0 30px;
    border: 1px solid $inputs-border-color;
    background-color: $inputs-bg-color;
    @media (max-width: 600px) {
      padding-left: 20px;
    }
  }
  &.lg-select {
    .ss-single-selected {
      @media (min-width: 481px) {
        height: 50px;
        border-radius: 25px;
      }
      &.ss-open-above,
      &.ss-open-below{
        @media (min-width: 481px) {
          border-radius: 25px 25px 0 0;
        }
      }
    }
    .ss-content {
      .ss-list {
        @media (min-width: 481px) {
          padding-top: 0;
        }
      }
    }
  }
  &.sm-select {
    .ss-single-selected {
      height: 30px;
      border-radius: 15px;
      padding: 0 14px;
      &.ss-open-above,
      &.ss-open-below{
        border-radius: 15px 15px 0 0;
      }
    }
    .ss-content {
      .ss-list {
        padding: 0 4px 8px;
        .ss-option {
          padding: 5px 10px;
        }
      }
    }
  }
}
.ss-main.has-overlay-select  {
  .ss-single-selected {
    position: relative;
    z-index: 6;
    &.ss-open-above,
    &.ss-open-below {
      &:before {
        content: '';
        display: block;
        position: fixed;
        width: 100%;
        height: 100%;
        background-color: rgba(255,255,255,.4);
        top: 0;
        left: 0;
        z-index: 6;
        cursor: default;
      }
    }
    .placeholder {
      position: relative;
      z-index: 6;
    }
    .ss-arrow {
      z-index: 6;
    }
  }
}

.sort-select{
  &.ss-main {
    .ss-single-selected {
      display: flex;
      //align-items: baseline;
      font-size: 14px;
      line-height: 1;
      &:before {
        content: 'Sort by:';
        white-space: nowrap;
        font-size: 14px;
        color: $text-color-light;
        margin-right: 6px;
        line-height: 1;
        display: inline-flex;
        height: 100%;
        align-items: center;
      }
    }
  }
}
//.simple-select
.simple-select {
  &.ss-main {
    position: static;
    font-size: 14px;
    .ss-single-selected {
      border: 0;
      padding-left: 0;
      padding-right: 0;
      &.ss-open-above,
      &.ss-open-below {
        border-color: $inputs-border-color;
      }
      .ss-arrow{
        margin-left: 8px;
        span{
          margin-top: -1px;
        }
      }

    }
    .ss-content {
      width: calc(100% + 2px);
      //border: 0;
      left: -1px;
    }
    .ss-content .ss-list{
      padding: 0 8px 8px;
    }
  }
}

.ss-main .ss-single-selected .ss-deselect {
  font-weight: 500;
  color: #ff6a00;
  font-size: 14px;
}

.selectize-input > input {
  -webkit-user-select: text !important;
}


