.page {
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  &.earth-bg {
    background-image: url("../images/earth.png");
    background-repeat: no-repeat;
    background-position: center top;
  }
}

.container {
  width: 100%;
  max-width: 1320px;
  margin: 0 auto;
  padding-left: 40px;
  padding-right: 40px;
  @media (max-width: 600px) {
    padding-left: 20px;
    padding-right: 20px;
  }
  &--narrow {
    max-width: 900px;
  }
}

.content {
  @extend .container;
  flex: 1 1 auto;
  padding-top: 60px;
  padding-bottom: 100px;

  &--flex {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
  }
  @media (max-width: 480px) {
    padding-top: 40px;
  }
  &--with-back-link {
    padding-top: 32px;
    @media (max-width: 480px) {
      padding-top: 18px;
    }
  }
  &--no-pad {
    padding-bottom: 0;
  }
  &--filter {
    @media (max-width: 540px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
    }
    .content-header {
      @media (max-width: 540px) {
        order: 0;
      }
    }
  }
  & > .back-link {
    margin-bottom: 24px;
    @media (max-width: 480px) {
      margin-bottom: 18px;
    }
  }
}

.content-header {
  text-align: center;
  margin-bottom: 50px;
  @media (max-width: 540px) {
    margin-bottom: 30px;
  }
  &__title {
    margin-bottom: 0;
  }
  &__intro {
    font-size: 18px;
    margin-top: 30px;
    @media (max-width: 480px) {
      font-size: 16px;
    }
  }
}

.back-link {
  width: 20px;
  height: 20px;
  display: inline-flex;
  align-items: center;
  .svg-icon {
    fill: $text-color-light
  }
}

.bg-wrapper{
  padding: 60px 0 100px;
  background-color: $bg-color;
  .page > & {
    flex: 1 1 auto;
  }
  @media (max-width: 768px) {
    padding-top: 23px;
  }
  @media (max-width: 480px) {
    padding-bottom: 30px;
  }
}

.avoid-break {
  break-inside: avoid;
}

.columns {
  @media (min-width: 769px) {
    column-count: 2;
    column-gap: 20px;
  }
}
