.slider-wrap{
  position: relative;
}

.slider-nav {
  &--aside {
    width: 100%;
    position: absolute;
    top: 50%;
    margin-top: -20px;
    .slider-nav-btn {
      position: absolute;
      top: 0;
      &--prev {
        left: -20px;
      }
      &--next {
        right: -20px;
      }
    }
  }
}

.slider-nav-btn {
  @extend .btn;
  width: 40px;
  height: 40px;
  background-color: #fff;
  border: 1px solid $text-color-light;
  border-radius: 50%;
  position: relative;
  &:hover {
    border-color: $theme-color;
  }
  &--prev {
    &:after {
      content: '';
      display: inline-block;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 3px 3px 3px 0;
      border-color: transparent $text-color-light transparent transparent;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
    }
    &:hover {
      &:after {
        border-color: transparent $theme-color transparent transparent;
      }
    }
  }
  &--next {
    &:after {
      content: '';
      display: inline-block;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 3px 0 3px 3px ;
      border-color: transparent transparent transparent $text-color-light;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
    }
    &:hover {
      &:after {
        border-color: transparent transparent transparent $theme-color;
      }
    }
  }
}
