.tariffs {
  display: flex;
  margin: -10px -10px 0;
  &__item {
    margin: 10px 10px 0;
    flex: 1 1 auto;
    @media (min-width: 481px) and (max-width: 768px) {
      min-width: 400px;
    }

    .screen {
      &__body {
        @media (max-width: 1000px) {
          padding: 30px 20px;
        }
      }
    }
  }

}

.tariff {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  flex: 1 1 auto;
  width: 100%;
  &__icon-wrap {
    height: 90px;
    margin-bottom: 20px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    @media (max-width: 480px) {
      width: 53px;
      height: 60px;
    }
  }
  &__term {
    font-size: 24px;
    font-weight: 500;
    line-height: 1;
    @media (max-width: 480px) {
      font-size: 16px;
    }
  }
  &__title {
    font-size: 16px;
    margin-top: 10px;
    line-height: 1;
    color: $text-color-light;
    @media (max-width: 480px) {
      font-size: 12px;
    }
  }
  &__rate {
    margin-top: 26px;
    & + .tariff__btn {
      margin-top: auto;
    }
  }
  &__free {
    font-size: 16px;
    margin-top: 4px;
    line-height: 1.3;
    color: $theme-color-4-light;
  }
  &__btn {
    margin-top: 30px;
    min-width: 160px;
    @media (max-width: 768px) {
      margin-top: 20px;
    }
    @media (max-width: 480px) {
      width: 100%;
    }
  }
}
