.tutors {
  position: relative;
  &__count {
    color: $text-color-light;
    @media (min-width: 1201px) {
      display: inline-block;
      position: absolute;
      top: 0;
      right: -180px;
    }
    @media (max-width: 1200px) {
      text-align: center;
      margin-bottom: 20px;
    }
  }
  .tutor-card {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.tutor-card {
  border-radius: 10px;
  background-color: #fff;
  padding: 40px;
  display: flex;
  font-size: 14px;
  @media (max-width: 700px) {
    padding: 20px;
  }
  @media (max-width: 640px) {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    font-size: 12px;
  }
  &__sidebar {
    width: 120px;
    flex: 0 0 120px;
    margin-right: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    @media (max-width: 640px) {
      //display: block;
      width: 100%;
      margin: 0;
      position: relative;
      align-items: flex-start;
    }
    .tutor-card__name,
    .tutor-card__location {
      @media (min-width: 641px) {
        display: none;
      }
    }
    a{
      color: $text-color-light;
    }
  }
  &__main {
    flex: 1 1 auto;
  }
  &__ava {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 120px;
    height: 120px;
    position: relative;
    margin-bottom: 29px;
    @media (max-width: 640px) {
      //float: left;
      width: 60px;
      height: 60px;
      //margin-right: 10px;
      //margin-bottom: 0;
      position: absolute;
      left: 0;
      top: 0;
    }
  }
  &__img {
    display: block;
    border-radius: 50%;
  }
  &__rating {
    @extend .rating;
    margin-top: -15px;
  }
  &__reviews {
    line-height: 1;
    display: inline-block;
    margin-bottom: 37px;
    @media (max-width: 640px) {
      display: block;
      margin-top: 6px;
      //margin-bottom: 20px;

      order: 3;
      margin-bottom: 1em;
    }
    @media (max-width: 400px) {
      margin-bottom: .6em;
      margin-top: 3px;
    }
  }
  &__lessons {
    line-height: 1;
    display: inline-block;
    margin-bottom: 14px;
    @media (max-width: 640px) {
      order: 4;
      margin-bottom: .3em;
    }
    @media (max-width: 480px) {
      margin-bottom: .65em;
    }
    @media (max-width: 400px) {
      margin-bottom: .5em;
    }
  }
  &__rate {
    margin-bottom: 12px;
    @media (max-width: 640px) {
      //margin-left: 12px;
      //height: 30px;
      //padding: 0 12px;
      //border-radius: 15px;

      order: 2;
      margin-left: 70px;
      background-color: transparent;
      padding: 0;
      height: auto;
      margin-top: 1em;
    }
    @media (max-width: 360px) {
      //margin-left: 6px;
    }
  }
  &__rate-level {
    display: inline-block;
    @media (max-width: 640px) {
      //margin-left: 12px;
      //position: relative;
      //top: -2px;
      //
      //display: none;
      position: absolute;
      left: 150px;
      top: 50px;
    }
    @media (max-width: 360px) {
      //margin-left: 6px;
    }
  }
  &__top {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding-bottom: 20px;
    margin-bottom: 30px;
    border-bottom: 1px solid $border-color;
    @media (max-width: 1000px) {
      flex-wrap: wrap;
    }
    @media (max-width: 640px) {
      padding-bottom: 20px;
      margin-bottom: 20px;
    }
  }
  &__params {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin-top: 5px;
    @media (max-width: 480px) {
      margin-top: 0;
      margin-bottom: 5px;
    }
    .param {
      margin-bottom: 3px;
      align-items: flex-start;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__header {
    margin-right: auto;
    @media (max-width: 1000px) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 15px;
    }
    @media (max-width: 480px) {
      margin-bottom: 5px;
    }
    .tutor-card__name,
    .tutor-card__location {
     @media (max-width: 640px) {
       display: none;
     }
   }
  }
  &__name {
    display: block;
    font-size: 18px;
    font-weight: 500;
    color: $text-color-base!important;
    @media (max-width: 640px) {
      font-size: 16px;
      line-height: 1;

      order: 0;
      margin-left: 70px;
    }
  }
  &__location {
    margin-top: 10px;
    @media (max-width: 640px) {
      //margin-top: 6px;

      order: 1;
      margin-left: 70px;
    }
  }
  &__controls {
    display: flex;
    //align-items: center;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-start;

    margin: 0 -5px;
    @media (max-width: 1000px) {
      flex-direction: row;
      //flex-wrap: wrap;
      margin: -5px 0 0;
      width: 100%;
    }
    //@media (max-width: 480px) {
    //  flex-wrap: wrap;
    //  margin: -10px 0 0;
    //  width: 100%;
    //}
    & > * {
      //margin: 0 5px;
      width: 160px;
      margin: 0 0 5px;
      white-space: nowrap;
      &:last-child {
        margin-bottom: 0;
        @media (max-width: 1000px) {
          margin-right: 0;
        }
      }

      @media (max-width: 1000px) {
        margin: 5px 5px 0 0;
        padding: 0 10px;
        //font-size: 12px;
        flex: 1 1 auto;
        width: auto;
      }
      @media (max-width: 600px) {
        font-size: 12px;
      }
      @media (max-width: 400px) {
        //font-size: 10px;
        height: 30px;
        border-radius: 15px;
      }
      //@media (max-width: 480px) {
      //  //margin-top: 10px;
      //  width: 100%;
      //}
      .svg-icon {
        @media (max-width: 400px) {
          width: 16px;
        }
      }
    }
  }
  &__chat-btn {
    @media (max-width: 460px) {
      flex: 1 1 auto;
    }
  }
  &__video-btn {
    border-radius: 18px;
  }
  &__schedule-btn {
    @media (max-width: 640px) {
      flex: 1 1 auto;
    }
  }
  &__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    @media (max-width: 750px) {
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
    }
    .param {
      @media (max-width: 750px) {
        margin-top: 8px;
      }
      &:first-child {
        @media (max-width: 750px) {
          margin-top: 0;
        }
      }
    }
  }
}


