.page-footer {
  background-color: $theme-color-2;
  padding-bottom: 50px;
  color: #ffffff;
  @media (max-width: 750px) {
    padding-bottom: 40px;
  }
  @media (max-width: 600px) {

  }
  &__inner {
    @extend .container;
    @media (max-width: 1024px) {
      padding-left: 0;
      padding-right: 0;
    }
  }
  &__top {
    position: relative;
    padding-top: 50px;
    display: flex;
    align-items: center;
    @media (max-width: 1600px) {
      padding-left: 40px;
      padding-right: 40px;
    }
    @media (max-width: 750px) {
      flex-wrap: wrap;
      padding-top: 34px;
    }
    @media (max-width: 600px) {
      padding-left: 20px;
      padding-right: 20px;
    }
    &:before {
      content: '';
      display: block;
      width: calc(100% - 80px);
      height: 1px;
      background-color: rgba(255,255,255,.2);
      position: absolute;
      left: 40px;
      top: 0;
      transition: opacity .25s;
      @media (max-width: 600px) {
        width: calc(100% - 40px);
        left: 20px;
      }
    }
    .secondary-menu {
      @media (max-width: 750px) {
        //margin-bottom: 20px;
      }
    }
    .guarantee {
      position: absolute;
      right: 40px;
      @media (max-width: 1600px) {
        position: static;
        flex-shrink: 0;
      }
      @media (max-width: 1024px) {
        width: 210px;
      }
      @media (max-width: 750px) {
        margin-top: 20px;
        width: auto;
      }
      @media (max-width: 600px) {
        font-size: 14px;
      }
    }
  }

  &__bottom {
    margin-top: 40px;
    position: relative;
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    padding: 0 40px;
    @media (max-width: 750px) {
      margin-top: 20px;
    }
    @media (max-width: 600px) {
      flex-wrap: wrap;
      padding-left: 20px;
      padding-right: 20px;
    }
  }
  &__logo {
    display: block;
    width: 123px;
    height: 28px;
    position: absolute;
    left: 40px;
    @media (max-width: 1600px) {
      position: static;
      flex-shrink: 0;
      margin-right: 53px;
    }
    @media (max-width: 750px) {
      margin-bottom: 20px;
    }
  }
  &__copy {
    position: absolute;
    left: 40px;
    font-size: 14px;
    color: $text-color-light;
    max-width: 50%;
    @media (max-width: 1600px) {
      position: static;
      flex-shrink: 0;
    }
    @media (max-width: 600px) {
      width: 100%;
      max-width: unset;
    }
    @media (max-width: 540px) {
      font-size: 12px;
    }
  }
  &__links {
    position: absolute;
    right: 40px;
    font-size: 14px;
    max-width: 50%;
    @media (max-width: 1600px) {
      position: static;
      flex-shrink: 0;
    }
    @media (max-width: 600px) {
      width: 100%;
      max-width: unset;
      margin-top: 20px;
    }
    @media (max-width: 540px) {
      font-size: 12px;
    }
    a {
      color: #ffffff;
      display: inline-block;
      margin-left: 46px;
      &:first-child {
        margin-left: 0;
      }
    }
  }
}
