.pages {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 50px -5px 0;
  @media (max-width: 480px) {
    margin-top: 20px;
  }
  &__item {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
    //width: 40px;
    min-width: 40px;
    height: 40px;
    border: 1px solid $text-color-light;
    margin: 0 5px;
    padding: 0 15px;
    border-radius: 20px;
    color: $theme-color-2;
    transition: none;
    flex: 0 0 auto;
    @media (max-width: 400px) {
      width: 36px;
      height: 36px;
      min-width: 36px;
    }
    &:hover {
      background-color: $theme-color;
      border-color: $theme-color;
      color: #ffffff;
    }
    &:nth-child(8) {
      @media (max-width: 540px) {
        display: none;
      }
    }
    &:nth-child(6) {
      @media (max-width: 400px) {
        display: none;
      }
    }
    &:nth-child(5) {
      @media (max-width: 360px) {
        display: none;
      }
    }
    &--prev {
      @media (min-width: 541px) {
        margin-right: 25px;
      }
      @media (max-width: 540px) {
        margin-right: auto;
      }
      &:before {
        content: '';
        display: inline-block;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 3px 3px 3px 0;
        border-color: transparent $text-color-light transparent transparent;
      }
      &:hover {
        &:before {
          border-color: transparent #ffffff transparent transparent;
        }
      }
    }
    &--next {
      @media (min-width: 541px) {
        margin-left: 25px;
      }
      @media (max-width: 540px) {
        margin-left: auto;
      }
      &:before {
        content: '';
        display: inline-block;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 3px 0 3px 3px;
        border-color: transparent transparent transparent $text-color-light;
        margin-left: 1px;
      }
      &:hover {
        &:before {
          border-color: transparent transparent transparent #ffffff;
        }
      }
    }
    &--dots {
      width: 40px;
      padding: 0;
      @media (max-width: 400px) {
        width: 36px;
        height: 36px;
        min-width: 36px;
      }
      &:hover {
        background-color: transparent;
        color: $text-color-base;
        border-color: $text-color-light;
      }
    }
    &--current {
      background-color: $theme-color-2;
      color: #ffffff;
      &:hover {
        background-color: $theme-color-2;
        color: #ffffff;
        border-color: $text-color-light;
      }
    }
  }
}
