//Color
$theme-color: #1f75af;
$theme-color-dark: #136094;
$theme-color-light: #F2FAFF;;
$theme-color-2: #1B1B1B;
$theme-color-3: #FFD24C;
$theme-color-4: #439F5A;
$theme-color-4-light: #55B76E;
$theme-color-4-dark: #2F8D47;
$bg-color: #FAFAFA;
$text-color-base: #1B1B1B;
$text-color-light: #8C8C8C;
$text-color-ultralight: #C1C1C1;


$link-color: $theme-color;
$link-color-hover: $theme-color-dark;

$error-color: red;
$border-color: #e7e7e7;

$inputs-border-color: $border-color;
$inputs-bg-color: #ffffff;
$inputs-focus-border-color: $theme-color;

$box-shadow: 0 12px 14px rgba(0, 0, 0, 0.1);
$box-shadow-light: 0 4px 6px rgba(0, 0, 0, 0.05);

//Buttons Colors
$primary-btn-color: $theme-color;
$primary-btn-hover-color: $theme-color-dark;
$primary-btn-active-color: $theme-color-dark;

$disabled-color: $text-color-light;

//Typography
$ff-base:          "Rubik", sans-serif;
$fs-base:          16px;
$fs-h1:            36px;
$fs-h2:            36px;
$fs-h3:            24px;
$fs-h4:            20px;
$fs-h5:            16px;
$line-height:      1.429em;
