.transactions-table {
  font-size: 14px;
  @media (max-width: 480px) {
    font-size: 12px;
  }
  &__header {
    display: flex;
    padding: 0 40px 20px;
    color: $text-color-light;
    @media (max-width: 1100px) {
      display: none;
    }
  }
  &__th {
    width: 20%;
    padding-left: 10px;
    &:first-child {
      padding-left: 0;
    }
  }
  &__body {

  }
  &__tr {
    display: flex;
    border-radius: 12px 12px 10px 10px;
    background-color: $bg-color;
    padding: 20px 40px;
    margin-bottom: 20px;
    @media (max-width: 1100px) {
      flex-wrap: wrap;
      position: relative;
    }
    @media (max-width: 700px) {
      flex-wrap: wrap;
      padding: 20px;
    }
    @media (max-width: 480px) {
      margin-bottom: 10px;
    }
    &:after {
      @media (max-width: 1100px) {
        content: '';
        display: block;
        width: calc(100% - 80px);
        height: 1px;
        background-color: $border-color;
        top: calc(50% + 5px);
        left: 40px;
        right: 0;
        position: absolute;
      }
      @media (max-width: 700px) {
        display: none;
      }
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__td {
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding-left: 30px;
    position: relative;
    @media (max-width: 700px) {
      width: 100%!important;
      padding: 20px 0;
      margin: 0!important;
      border-bottom: 1px solid $border-color;
    }
    @media (max-width: 480px) {
      padding: 14px 0;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
    }
    &:after {
      content: '';
      display: inline-block;
      width: 1px;
      height: 20px;
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      background-color: $border-color;
      @media (max-width: 1100px) {
        height: 52px;
      }
      @media (max-width: 700px) {
        display: none!important;
      }
    }
    &:before {
      @media (max-width: 1100px) {
        content: attr(data-th);
        display: block;
        color: $text-color-light;
        font-size: 14px;
      }
      @media (max-width: 480px) {
        font-size: 12px;
        width: 37px;
        margin-right: 23px;
      }
    }
    & > div + div {
      margin-top: 3px;
    }
    &:first-child {
      width: calc(20% - 20px);
      padding-left: 0;
      border-left: 0;
      &:after {
        display: none;
      }

      @media (max-width: 1100px) {
        width: 25%;
        margin-bottom: 25px;
      }
      @media (max-width: 700px) {
        padding-top: 0;
      }
    }
    &:nth-child(2) {
      @media (max-width: 1100px) {
        width: 25%;
        margin-bottom: 25px;
      }
    }
    &:nth-child(3) {
      @media (max-width: 1100px) {
        width: 50%;
        margin-bottom: 25px;
      }
    }
    &:nth-child(4) {
      @media (max-width: 1100px) {
        width: 50%;
        padding-left: 0;
        margin-top: 25px;
      }
      &:after {
        @media (max-width: 1100px) {
          display: none;
        }
      }
    }
    &:nth-child(5) {
      @media (max-width: 1100px) {
        width: 50%;
        margin-top: 25px;
      }
    }
    &:last-child {
      @media (max-width: 700px) {
        border-bottom: 0;
        padding-bottom: 0;
      }
    }
    .transaction-state {
      @media (max-width: 1100px) {
        display: flex;
        div {
          &:first-child {
            margin-right: 12px;
          }
        }
      }
      @media (max-width: 700px) {
        flex-direction: column;
        line-height: 1.6;
      }
    }
  }
}

.table-controls {
  display: flex;
  align-items: center;
  margin: 0 -5px;
  button {
    @extend .btn;
    width: 20px;
    height: 20px;
    margin: 0 5px;
    .svg-icon {
      fill: $text-color-light;
    }
    &:hover {
      .svg-icon {
        fill: $theme-color;
      }
    }
  }
}

.table-sort-btn {
  @extend .btn;
  width: 20px;
  height: 20px;
  &:before {
    content: '';
    display: inline-block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 4px 4px 4px;
    border-color: transparent transparent $text-color-light transparent;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    margin: auto;
  }
  &:after {
    content: '';
    display: inline-block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 4px 4px 0 4px;
    border-color: $text-color-light transparent transparent transparent;
    right: 0;
    left: 0;
    bottom: 0;
    margin: auto;
  }
}

table.stripe-tbl {
  tbody {
    tr{
      &:nth-child(2n) {
        td {
          background-color: #D6D8DB;
        }
      }
    }
  }
}

table {
  td {
    .person {
      margin-top: 4px;
      cursor: pointer;
    }
  }
}
